import {Component, OnInit, Input, SimpleChanges, OnChanges} from '@angular/core';
import {FileContentService} from '../file-content.service';

@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.css'],
  providers: [FileContentService]
})
export class ViewComponent implements OnInit, OnChanges {

  @Input() element;
  content: string;
  pageContent: string[];
  numberPages: number;
  currentPage: number;
  maxLinesPerPage: number;

  constructor(private fileContentService: FileContentService) {
    this.pageContent = [];
    this.maxLinesPerPage = 5000;
  }

  ngOnInit() {

  }

  ngOnChanges(changes: SimpleChanges) {
    if (this.element.path) {
      this.fileContentService.getContent(this.element.path)
        .subscribe(
          content => {
            this.content = content;
            this.paginate();
          },
          error => console.log(error)

        );
    }
  }

  private paginate() {
    const maxLinesPerPage = this.maxLinesPerPage;

    this.currentPage = 1;
    const lines = this.content.split(/\r\n|\r|\n/);

    this.numberPages = Math.ceil(lines.length / maxLinesPerPage);
    for (let i = 0; i < this.numberPages; i++) {
      this.pageContent[i] = '';
      for (let j = 0; j < maxLinesPerPage; j++) {
        const line = lines[i * maxLinesPerPage + j];
        if (line == null) {
          break;
        }
        this.pageContent[i] += lines[i * maxLinesPerPage + j] + '\n';
      }
    }

  }

  private searchWord(searchItem: string) {
    console.log('Search triggered: '  + searchItem);

    if (this.content.search(searchItem) > -1) {
      for (let i = 0; i < this.numberPages; i++) {
        const isOnPage = this.pageContent[i].search(searchItem);
        if (isOnPage > -1) {
          this.currentPage = i + 1;
          return;
        }
      }
    }
  }

}



