import {Component, OnInit, Input, ViewEncapsulation} from '@angular/core';
import {GraphData} from '../../helper/graph-data';
import {D3, D3Service} from 'd3-ng2-service';
import {GraphCommunicationService} from '../../graph-communication.service';
import {GraphHelperService} from '../../helper/graph-helper.service';
import * as Awesomplete from 'awesomplete';
import {EventCommunicationService} from '../../event-communication.service';
import {SearchEvent} from '../../helper/SearchEvent';

@Component({
  selector: 'app-graph-search',
  templateUrl: './graph-search.component.html',
  styleUrls: ['./graph-search.component.css'],
  encapsulation : ViewEncapsulation.None
})
export class GraphSearchComponent implements OnInit {

  private _graph: GraphData;

  private d3: D3;


  @Input()
  set graph(graph: GraphData) {
    this._graph = graph;
    if (graph != null) {
      this.resetSearch();
      this.setUpSearch();
    }
  }

  get graph(): GraphData {
    return this._graph;
  }

  constructor(
    d3Service: D3Service,
    private graphCommunicationService: GraphCommunicationService,
    private eventCommunicationService: EventCommunicationService
  ) {
    this.d3 = d3Service.getD3();

  }

  ngOnInit() {
  }

  private resetSearch() {
    if (document.getElementById('main')) {
      const controls = document.getElementById('main').getElementsByClassName('controls');
      for (let i = 0; i < controls.length; i++) {
        const awesomplete = controls[i].getElementsByClassName('awesomplete');
        if (controls[i].getElementsByClassName('awesomplete').length > 0) {
          const element = controls[i];
          element.removeChild(awesomplete[0]);

          const search = document.createElement('input');
          search.setAttribute('id', 'search');
          search.setAttribute('type', 'search');
          search.setAttribute('placeholder', 'search');

          element.appendChild(search);
        }
      }

    }
  }

  private setUpSearch() {
    const graph = this.graph;
    const d3 = this.d3;
    const graphCommunicationService = this.graphCommunicationService;

    if (document.getElementById('search')) {
      const search = document.getElementById('search');
      /* global Awesomplete */
      const awe = new Awesomplete(search, {

        minChars: 1,
        list: graph.elements,
        filter: function(item: any, input) {
          return GraphHelperService.getNodeName(item.value).toLowerCase().indexOf(input.toLowerCase()) > -1;
        },
        sort: function(a: any, b: any) {
          const nameA = GraphHelperService.getNodeName(a.value),
            nameB = GraphHelperService.getNodeName(b.value);

          return nameA < nameB ? -1 : nameA > nameB ? 1 : 0;
        },
        item: function(item: any, input) {

          const li = d3.select(document.createElement('li'));
          const nodeTypes = graphCommunicationService.nodeTypes;
          li.style('background', nodeTypes[item.value.type]);

          const name = GraphHelperService.getNodeName(item.value);
          const text = input === '' ? name : name.replace(new RegExp(Awesomplete.$.regExpEscape(input.trim()), 'gi'), '<mark>$&</mark>');

          const selectedNodes = graphCommunicationService.selectedNodes;
          const isSelected = selectedNodes.some(function(n) {
            return n.id === item.value.id;
          });
          if (isSelected) {
            li.classed('selected', true);
          }

          const nameElement = li.append('div');

          nameElement.append('div').classed('name', true)
            .html(text);

          nameElement.append('div').classed('addButton', true)
            .html(isSelected ? '&minus;' : '+')
            .attr('title', isSelected ? 'remove from selection' : 'add to selection');

          li.append('div').classed('fullName', true)
            .html(item.value.name);

          return li.node();

        },
        replace: function() {
          this.input.value = '';
        }
      });
      search.addEventListener('awesomplete-select', e => {

        const selectEvent: SearchEvent = new SearchEvent(e['text'].value, e['origin'].className === 'addButton');
        this.eventCommunicationService.selectSearchItem(selectEvent);
      });

    }

  }

}



