import {Component, OnInit, Input, ElementRef, ViewEncapsulation} from '@angular/core';
import {TabTree} from '../tab-tree';
import {D3, D3Service} from 'd3-ng2-service';
import {Selection} from 'd3-selection';
import {GraphHelperService} from '../../helper/graph-helper.service';
import {EventCommunicationService} from '../../event-communication.service';
import {GraphCommunicationService} from '../../graph-communication.service';

/**
 * Nur Darstellung, bekommt direkt Daten
 */
@Component({
  selector: 'app-tab-content',
  templateUrl: './tab-content.component.html',
  styleUrls: ['./tab-content.component.css'],
  encapsulation: ViewEncapsulation.None,
})
export class TabContentComponent implements OnInit {

  private _tree: TabTree<any>;
  private d3: D3;
  private parentNativeElement: any;

  @Input()
  set tree(tree: TabTree<any>) {
    this._tree = tree;

    if (tree != null) {
      this.updateTab();

    }

  };

  get tree(): TabTree<any> {
    return this._tree;
  }

  constructor(
    element: ElementRef,
    d3Service: D3Service,
    private eventCommunicationService: EventCommunicationService,
    private graphCommunicationService: GraphCommunicationService
  ) {
    this.d3 = d3Service.getD3();
    this.parentNativeElement = element.nativeElement;
  }

  ngOnInit() {
  }

  private updateTab() {
    const tree = this.tree;
    const d3 = this.d3;
    let d3ParentElement: Selection<any, any, any, any>;

    if (this.parentNativeElement != null) {
      d3ParentElement = d3.select(this.parentNativeElement);
      const tab = d3ParentElement.select('.treeView');

      this.createTreeView(tree.children, tab);

    }

  }

  private createTreeView(items, parent) {
    const d3 = this.d3;

    const itemList = items.map(i => {
      return i.element;
    });

    const elements = parent.append('ul').selectAll('li')
      .data(itemList).enter()
      .append('li')
      .sort((a, b) => {
        if (a.type === b.type) {
          if (GraphHelperService.getNodeName(a) < GraphHelperService.getNodeName(b)) {
            return -1;
          } else if (GraphHelperService.getNodeName(b) < GraphHelperService.getNodeName(a)) {
            return 1;
          }
          return 0;
        }
        const sortOrder = Object.keys(this.graphCommunicationService.nodeTypes);
        return sortOrder.indexOf(a.type) - sortOrder.indexOf(b.type);
      });

    const nodeContent = elements.append('div');

    nodeContent.append('div').style('background', d => {

      return this.graphCommunicationService.nodeTypes[d.type];

    }).text(d => {

      return GraphHelperService.getNodeName(d);

    }).on('click', d =>  {

      this.eventCommunicationService.selectTreeItem(d);

    }).classed('treeNodeCaption', true);

    const object = this;

    elements.each(function(d) {
      const element = items.find(i => {
        return GraphHelperService.elementEquals(i.element, d);
      });
      const subItems = element.children;

      if (subItems.length) {

        const item = d3.select(this);

        item.select('div').insert('div', '.treeNodeCaption')
          .attr('class', 'treeButton')
          .html('&minus;')
          .on('click', function() {

            const subList = item.select('ul'),
              me = d3.select(this),
              status = subList.style('display');
            subList.style('display', status === 'none' ? null : 'none');
            me.html(status === 'none' ? '&minus;' : '+');
          });

        object.createTreeView(subItems, d3.select(this));
      }
    });

    this.collapseTab();

  }

  private collapseTab() {
    const root =  this.d3.select(this.parentNativeElement).select('.treeView').select('ul');

    root.selectAll('ul').style('display', 'none');
    root.selectAll('div').select('.treeButton').html('+');
  }

  private expandTab() {
    const root =  this.d3.select(this.parentNativeElement).select('.treeView').select('ul');

    root.selectAll('ul').style('display', null);
    root.selectAll('div').select('.treeButton').html('&minus;');
  }



}



