import { Component, OnInit } from '@angular/core';
import {SelectedProjectService} from '../../selected-project.service';

@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.css']
})
export class NavigationComponent implements OnInit {

  private graphActive: boolean;
  private metricActive: boolean;
  private showWarning: boolean;

  constructor(private selectedProjectService: SelectedProjectService) {
    selectedProjectService.properties$.subscribe(
      prop => {
        this.graphActive = prop.graph.tabs.length > 0;
        this.metricActive = prop.metrics.usedOd !== '';
        this.showWarning = !this.graphActive || !this.metricActive;
      }
    )
  }

  ngOnInit() {
  }


  private resetPage() {
    this.selectedProjectService.setStateToDefault();
  }

}



