export class MetricGraphicalProp {
  readonly name: string;

  readonly graphic: string[];
  readonly defaultGraphic: string;
  readonly showMultipleMetrics: boolean;
  readonly relMetric: string;
  readonly relMetricX: string;
  readonly relMetricY: string;
  readonly coloring: boolean;
  readonly coloringMin: number;
  readonly coloringMax: number;

  constructor(
    name: string,
    graphic: string[] = [],
    defaultGraphic: string = '',
    showMultipleMetric: boolean = false,
    relMetric: string,
    relMetricX: string,
    relMetricY: string,
    coloring: boolean = false,
    coloringMin: number,
    coloringMax: number
  ) {
  this.name = name;
  this.graphic = graphic;
  this.defaultGraphic = defaultGraphic;
  this.showMultipleMetrics = showMultipleMetric;
  this.relMetric = relMetric;
  this.relMetricX = relMetricX;
  this.relMetricY = relMetricY;
  this.coloring = coloring;
  this.coloringMin = coloringMin;
  this.coloringMax = coloringMax;
  }
}



