export class TabTree<T> {

  private _element: T;
  private _children: TabTree<T>[];

  constructor(element: T) {
    this._element = element;
    this._children = [];
  }

  get element(): T {
    return this._element;
  }

  get children(): TabTree<T>[] {
    return this._children;
  }

  // Add Child to Tree, returns the new TreeElement;
  public addChild(child: T): TabTree<T> {
    const newLength = this._children.push(new TabTree<T>(child));
    return this._children[newLength - 1];
  }
}



