import { Injectable } from '@angular/core';
import {GraphData} from './graph-data';

@Injectable()
export class GraphHelperService {

  public static elementEquals(a, b) {
    if (a === null || b === null) {
      return a === b;
    }

    if (typeof a !== 'object' || typeof b !== 'object') {
      return false;
    }

    if (!a.hasOwnProperty('id') || !b.hasOwnProperty('id')) {
      return false;
    }

    return a.id === b.id;

  }

  public static preProcessGraph(graph): GraphData {
    if (graph.links.length > 0 && graph.links[0].name === 'MergedLink') {
      graph.links = graph.links.map(function (l) {
        return {
          source: graph.elements[l.source - 1],
          target: graph.elements[l.target - 1],
          forwardLinks: l.forwardLinks,
          backwardLinks: l.backwardLinks
        };
      });
    } else {
      graph.links = GraphHelperService.preProcessData(graph.elements, graph.links);
    }

    return graph;

  }

  /*
  Combine links
   */
  private static preProcessData  (elements, links) {

    const groupedLinks = [];

    links.forEach(function(link) {

      const s = elements[link.source - 1];
      const t = elements[link.target - 1];

      if (!groupedLinks.some(function(l) {

            if (l.source === s && l.target === t) {

              if (l.forwardLinks[link.name]) {

                ++l.forwardLinks[link.name];

              } else {

                l.forwardLinks[link.name] = 1;
              }
              return true;

            } else if (l.source === t && l.target === s) {

              if (l.backwardLinks[link.name]) {

                ++l.backwardLinks[link.name];

              } else {

                l.backwardLinks[link.name] = 1;
              }
              return true;
            }
          })) {
        const groupedLink = {
          source: s,
          target: t,
          forwardLinks: {},
          backwardLinks: {}
        };
        groupedLink.forwardLinks[link.name] = 1;
        groupedLinks.push(groupedLink);
      }
    });

    return groupedLinks;

  }

  public static getNodeName(e) {

    return e.attributes.simpleName || e.attributes.name;
  }

  public static isEmpty(o) {

    for (const key in o) {
      if (o.hasOwnProperty(key)) {
        return false;
      }
    }
    return true;
  }

  constructor() { }

}



