import {Component, OnInit, Input, ViewChild} from '@angular/core';
import {MetricService} from '../metric.service';
import {Metric} from '../helper/metric';
import {MetricDataItem} from '../helper/metric-data-item';
import {D3Service, D3} from 'd3-ng2-service';
import {NgbCarousel} from '@ng-bootstrap/ng-bootstrap';
import {ColorService} from '../../color.service';
import {MetricGraphicalProp} from '../../helper/metricGraphicalProp';

@Component({
  selector: 'app-metric-charts',
  templateUrl: './metric-charts.component.html',
  styleUrls: ['./metric-charts.component.css']
})
export class MetricChartsComponent implements OnInit {

  @ViewChild(NgbCarousel)
  private chartCarousel;

  private _metricToShow: any;
  private _onlyHighest: boolean;
  private _highestCount: number;
  private _properties: any;

  private d3: D3;

  private metrics: Metric[];
  private currentMetric: Metric;
  private currentProperties: MetricGraphicalProp;
  private displayCharts: boolean;

  private displayPieChart: boolean;
  private displayVertBarChart: boolean;
  private displayHorBarChart: boolean;
  private displayScatterChart: boolean;

  private view: any[] = [650, 400];
  private singleData: any[];
  private multiData: any[];
  private scatterData: any[];
  private colorScheme: any ;

  private scatterXLabel: string;
  private scatterYLabel: string;

  @Input()
  set metricToShow(metric: any) {
    this._metricToShow = metric;

    this.update();

  }

  @Input()
  set onlyHighest(onlyHighest: boolean) {
    this._onlyHighest = onlyHighest;

    if (this.currentMetric != null) {
      this.calcChartData(this.currentMetric);
    }
  }

  @Input()
  set highestCount(count: number) {
    this._highestCount = count;

    if (this.currentMetric != null) {
      this.calcChartData(this.currentMetric);
    }
  }

  @Input()
  set properties(properites: any) {
    this._properties = properites;

    this.update();
  }

  get metricToShow(): any {
    return this._metricToShow;
  }

  get onlyHighest(): boolean {
    return this._onlyHighest;
  }

  get highestCount(): number {
    return this._highestCount;
  }

  get properties(): any {
    return this._properties;
  }

  constructor(
    private metricService: MetricService,
    private d3Service: D3Service,
    private colorService: ColorService
  ) {

    this.d3 = d3Service.getD3();
    this.setColorScheme();

    metricService.metrics$.subscribe(
      metrics => {
        if (metrics.length > 0) {
          this.metrics = metrics;
        }
      }
    );

  }

  private setColorScheme() {
    const domain = this.colorService.getColorScheme();
    this.colorScheme = { domain : domain};

  }

  ngOnInit() {
  }

  private onSelect(event) {
    console.log(event);
  }


  private update() {

    if (this.metricToShow === 'Metric') {
      this.displayCharts = false;
    }

    if (this.metricToShow !== 'Metric' && this.properties != null) {
      this.updateGraphics();
    }
  }

  private updateGraphics() {

    this.currentProperties = this.properties.find(p => {
      return p.name === this.metricToShow;
    });
    if (this.currentProperties == null) {
      console.log('Error: Metric not displayable');
      return;
    }

    const currentMetric = this.metrics.find(m => {
      return m.name === this.metricToShow;
    });
    this.currentMetric = currentMetric;

    if (this.currentProperties.graphic.length > 0) {
      this.calcChartData(currentMetric);

      this.displayVertBarChart = (this.currentProperties.graphic.indexOf('Vertical Bar Chart') !== -1);
      this.displayPieChart = (this.currentProperties.graphic.indexOf('Pie Chart') !== -1);
      this.displayScatterChart = (this.currentProperties.graphic.indexOf('Scatter Chart') !== -1);
      this.displayHorBarChart = (this.currentProperties.graphic.indexOf('Horizontal Bar Chart') !== -1);

      this.displayCharts = true;
      setTimeout(() => {
        switch (this.currentProperties.defaultGraphic) {
          case 'Horizontal Bar Chart' :
            this.chartCarousel.select('horBarChart');
            break;
          case 'Vertical Bar Chart' :
            this.chartCarousel.select('vertBarChart');
            break;
          case 'Pie Chart' :
            this.chartCarousel.select('pieChart');
            break;
          case 'Scatter Chart' :
            this.chartCarousel.select('scatterChart');
            break;
          default :
            this.displayCharts = false;
            console.log('Unsupported Default Chart');
        }

      }, 0);

    } else {
      this.displayCharts = false;
    }

  }

  private calcChartData(metric: Metric) {
    let metricData: MetricDataItem[] = metric.data;
    metricData = metricData.sort((a, b) => {
      return b.value - a.value;
    });

    let count = metricData.length;
    if (this.onlyHighest) {
      count = this.highestCount;
    }

    this.calcSingleData(metricData, count);
    this.calcMultiData(metricData, count, metric.name);
    this.calcScatterData(metricData, count, metric.name);

  }

  private calcSingleData(metricData: MetricDataItem[], count: number) {
    const result = [];
    let value = {};

    for (let i = 0; i < count; i++) {
      value = {name : metricData[i].name, value: metricData[i].value};
      result.push(value);

    }

    this.singleData = result;
  }

  private calcMultiData(metricData: MetricDataItem[], count: number, metricName: string) {

    const result = [];

    for (let i = 0; i < count; i++) {
      const series = [];
      series.push({name : metricName, value : metricData[i].value});
      const element = {name : metricData[i].name, series : series};
      result.push(element);
    }


    if (this.currentProperties.showMultipleMetrics) {
      const secondMetric = this.metrics.find(m => {
        return m.name === this.currentProperties.relMetric;
      });

      if (secondMetric != null) {

        const secondMetricData = secondMetric.data;

        for (const artifact of result) {
          const item = secondMetricData.find(d => {
            return d.name === artifact.name;
          });
          let seriesItem = {};
          if (item) {
            seriesItem = {name: secondMetric.name, value: item.value};
          } else {
            seriesItem = {name: secondMetric.name, value: 0};
          }
          artifact.series.push(seriesItem);
        }
      }
    }

    this.multiData = result;
  }

  private calcScatterData(metricData: MetricDataItem[], count: number, metricName: string) {
    const series = [];

    for (let i = 0; i < count; i++) {
      const element = {name : metricData[i].name, r : metricData[i].value};
      series.push(element);
    }

    const xMetric = this.metrics.find(m => {
      return m.name === this.currentProperties.relMetricX;
    });
    const yMetric = this.metrics.find(m => {
      return m.name === this.currentProperties.relMetricY;
    });

    if (xMetric != null && yMetric != null) {
      const xMetricData = xMetric.data;
      const yMetricData = yMetric.data;

      this.scatterXLabel = xMetric.name;
      this.scatterYLabel = yMetric.name;

      for (const element of series) {
        const xItem = xMetricData.find(d => {
          return d.name === element.name;
        });
        if (xItem) {
          element.x = xItem.value;
        } else {
          element.x = 0;
        }

        const yItem = yMetricData.find(d => {
          return d.name === element.name;
        });
        if (yItem) {
          element.y = yItem.value;
        } else {
          element.y = 0;
        }
      }
    }

    const result = [];

    const value = {name : metricName, series : series};
    result.push(value);

    this.scatterData = result;
  }

}



