import { Injectable } from '@angular/core';

import { Objectdiagram} from './helper/objectdiagram';

import {HttpService} from './helper/httpService';
import {Subject, BehaviorSubject, Observable} from 'rxjs/Rx';
import {Http} from '@angular/http';
import {SelectedProjectService} from './selected-project.service';

@Injectable()
export class SelectedOdService extends HttpService {

  private _selectedOd = new Subject<Objectdiagram>();
  private _selectedOdData: Objectdiagram;
  private _odDropdownDisabled = new BehaviorSubject<boolean>(false);

  selectedOd$ = this._selectedOd.asObservable();
  odDropdownDisabled$ = this._odDropdownDisabled.asObservable();

  private jsonData: JSON;


  constructor(private http: Http, private selectedProjectService: SelectedProjectService) {
    super();
    this.jsonData = JSON.parse('{"elements" : [], "links": []}');

    selectedProjectService.folderStructure$.subscribe(
      () => {
        // Set default OD
        selectedProjectService.getProperties().subscribe(
          properties => {
            const defaultOdPath = selectedProjectService.getSelectedProject().getValue().path + properties.main.defaultFile;
            const defaultOd = selectedProjectService.getOdFromPath(defaultOdPath);

            if (defaultOd != null) {
              this.setSelectedOd(defaultOd);
            }
          }
        );
      },
      error => console.log(error)
    );

  }

  public enableOdDropdown(isEnabled: boolean) {
    this._odDropdownDisabled.next(!isEnabled);
  }

  public setSelectedOd(od: Objectdiagram) {
    this.selectedProjectService.getDisplayableOdList().then(
      odList => {
        if (odList.some(element => {
            return element.name === od.name;
          })) {
          this._selectedOdData = od;

          this.loadJSON().subscribe(
            data => {
              this.jsonData = data;
              this._selectedOd.next(od);
            }
          );
        }
      });

  }

  public getSelectedOd(): Objectdiagram {
    return this._selectedOdData;
  }

  private loadJSON(): Observable<JSON> {
    const od = this.getSelectedOd();
    if (od == null) {
      return Observable.empty ();
    }
    const path = od.jsonPath;

    return this.http.get(path)
      .map(SelectedOdService.extractData)
      .catch(SelectedOdService.handleError);
  }

  public getJsonData(): Observable<JSON> {
    return Observable.of (this.jsonData);
  }



}



