import { Component, OnInit } from '@angular/core';
import { AllProjectsService } from '../../all-projects.service';
import { SelectedProjectService } from '../../selected-project.service';
import { Project } from '../../helper/project';
import { Objectdiagram } from '../../helper/objectdiagram';
import {SelectedOdService} from '../../selected-od.service';

@Component({
  selector: 'app-dropdown',
  templateUrl: 'dropdown.component.html',
  styleUrls: ['dropdown.component.css']
})
export class DropdownComponent implements OnInit {
  private projects: Array<Project>;
  public odDropdown: Array<Objectdiagram>;
  private odDropdownDisabled: boolean;

  private selectedProject: Project;
  private selectedOd: Objectdiagram;

  constructor(private allProjectsService: AllProjectsService,
              private selectedProjectService: SelectedProjectService,
              private selectedOdService: SelectedOdService) {

    this.selectedProject = new Project('Select Project', '');
    this.selectedOd = new Objectdiagram('Select OD', '', '');

    selectedProjectService.selectedProject$.subscribe(
      selectedProject => {
        this.selectedProjectChanged(selectedProject);
      }
    );

    selectedProjectService.odList$.subscribe(
      () => {
        this.setDisplayableOds();
      }
    );

    selectedOdService.selectedOd$.subscribe(
      selectedOd => {
        this.selectedOdChanged(selectedOd);
      }
    );

    selectedOdService.odDropdownDisabled$.subscribe(
      isDisabled => this.odDropdownDisabled = isDisabled
    )


  }

  ngOnInit() {
    this.allProjectsService.getProjects()
      .subscribe(
        allProjects => this.projects = allProjects,
        error => console.log(error)
      );

  }

  selectOd(od) {
    this.selectedOdService.setSelectedOd(od);

  }

  selectProject(project) {
    this.selectedProjectService.setSelectedProject(project);
  }

  selectedProjectChanged(newProject) {
    this.selectedProject = newProject;
  }

  selectedOdChanged(newOd) {
    this.selectedOd = newOd;
  }

  public setDisplayableOds() {
    this.selectedProjectService.getDisplayableOdList().then(list => {
      this.odDropdown = list;
    });
  }

}



