import {NgModule, ModuleWithProviders} from '@angular/core';
import { CommonModule } from '@angular/common';
import {Routes, RouterModule} from '@angular/router';
import { NavigationComponent } from './navigation/navigation.component';
import {SelectedProjectService} from '../selected-project.service';
import {ProjectDateComponent} from './project-date/project-date.component';
import {DropdownComponent} from './dropdown/dropdown.component';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {AllProjectsService} from '../all-projects.service';
import {SelectedOdService} from '../selected-od.service';

export const routes: Routes = [
  {
    path: '',
    redirectTo: '/textual',
    pathMatch: 'full'
  },
  {
    path: 'textual',
    loadChildren: 'app/textual/textual.module#TextualModule'
  },
  {
    path: 'metrics',
    loadChildren: 'app/metric/metric.module#MetricModule'
  },
  {
    path: 'graph',
    loadChildren: 'app/graph/graph.module#GraphModule'
  },
  {
    path: 'cypher',
    loadChildren: 'app/cypher/cypher.module#CypherModule'
  },
  {
    path: 'modules',
    loadChildren: 'app/modules/modules.module#ModulesModule'
  }
];

@NgModule({
  imports: [
    CommonModule,
    NgbModule,
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule, NavigationComponent],
  declarations: [
    NavigationComponent,
    ProjectDateComponent,
    DropdownComponent
  ],

})
export class NavigationModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: NavigationModule,
      providers: [
        AllProjectsService,
        SelectedProjectService,
        SelectedOdService
      ]
    };
  }
}



