import { Component, OnInit } from '@angular/core';
import {CypherDataService} from "../cypher-data.service";

@Component({
  selector: 'app-cypher',
  templateUrl: './cypher.component.html',
  styleUrls: ['./cypher.component.css']
})
export class CypherComponent implements OnInit {

  public output: any;
  public input: string;
  public graph: any;

  constructor(private cypherDataService : CypherDataService) {
    this.input = "MATCH (n) OPTIONAL MATCH (n)-[r]-() RETURN n,r;";
  }

  ngOnInit() {
    this.executeStatement();
    this.getGraph();
  }

  executeStatement() {
    this.cypherDataService.executeStatement(this.input).then(
      res => {
        this.output = res;
      }
    );
  }

  getGraph() {
    this.cypherDataService.getGraph().then(graph => {
      this.graph = graph;
    })
  }

}



