import {Component, OnInit, Input, ViewEncapsulation} from '@angular/core';
import {GraphData} from '../../helper/graph-data';
import {SelectedProjectService} from '../../../selected-project.service';
import {GraphCanvasHelperService} from '../../helper/graph-canvas-helper.service';
import * as cola from 'webcola'
import {D3, D3Service} from 'd3-ng2-service';
import {GraphCommunicationService} from '../../graph-communication.service';
import {EventCommunicationService} from '../../event-communication.service';
import {GraphHelperService} from '../../helper/graph-helper.service';
import {GraphTreeService} from '../../helper/graph-tree.service';

@Component({
  selector: 'app-graph-canvas',
  templateUrl: './graph-canvas.component.html',
  styleUrls: ['./graph-canvas.component.css'],
  encapsulation : ViewEncapsulation.None
})
export class GraphCanvasComponent implements OnInit {

  private d3: D3;

  private _graph: GraphData;
  private _isHierarchical: boolean;

  private _layoutActive: boolean;
  private _nodeCollision: boolean;
  private _allLinks: boolean;
  private _clickToAdd: boolean;

  private groupItemType: string;
  private subGroupItemType: string[];
  private groupChildLinkTypes: string[];
  private groupCrossLinkTypes: string[];

  private hierarchicalGraph;
  private groupGraphs;
  private fullGraph;
  private currentGraph;
  private layout;
  private drag;
  private fixedNodes;
  private svg;
  private path;
  private node;
  private group;
  private nodeMargin;
  private viewBox;
  private zoom;
  private selectedNodes;
  private svgGroup;
  private groupMargin;
  private nodePadding;
  private stopLayoutTimeout;

  private status;
  private selectedTypes: string[];

  @Input()
  set graph(graph: GraphData) {
    this._graph = graph;

    if (graph != null) {
      this.initView();
    }

  };

  @Input()
  set isHierarchical(isHierarchical: boolean) {
    this._isHierarchical = isHierarchical;

    if (this.graph != null && this.isHierarchical != null) {
      this.updateGraph();
    }
  }

  @Input()
  set layoutActive(layoutActive: boolean) {
    this._layoutActive = layoutActive;

    if (this.graph != null) {
      this.addDragBehaviour(this.node, this.layout, this.drag, this.layoutActive);

      if (!this.layoutActive) {
        this.layout.stop();
      } else {
        this.startLayout(this.currentGraph);
      }
    }

  };

  @Input()
  set nodeCollision(nodeCollision: boolean) {
    this._nodeCollision = nodeCollision;

    if (this.graph != null && this.layoutActive) {
      this.makeGraphWithMessage();
    }

  };

  @Input()
  set allLinks(allLinks: boolean) {
    this._allLinks = allLinks;

    if (this.graph != null) {
      this.makeGraphWithMessage();
    }

  };

  @Input()
  set clickToAdd(clickToAdd: boolean) {
    this._clickToAdd = clickToAdd;

  };


  get graph(): GraphData {
    return this._graph;
  }

  get isHierarchical(): boolean {
    return this._isHierarchical;
  }

  get layoutActive(): boolean {
    return this._layoutActive;
  }

  get nodeCollision(): boolean {
    return this._nodeCollision;
  }

  get allLinks(): boolean {
    return this._allLinks;
  }

  get clickToAdd(): boolean {
    return this._clickToAdd;
  }


  constructor(
    d3Service: D3Service,
    private selectedProjectService: SelectedProjectService,
    private graphCanvasHelperService: GraphCanvasHelperService,
    private eventCommunicationService: EventCommunicationService,
    private graphCommunicationService: GraphCommunicationService,
    private graphTreeService: GraphTreeService
  ) {
    this.d3 = d3Service.getD3();
    this.selectedTypes = [];

    // TODO Double Check
    /*selectedProjectService.properties$.subscribe(
      properties => {
        this.groupItemType = properties["graph"].groupItemType;
        this.subGroupItemType = properties["graph"].subGroupItemType;
        this.groupChildLinkTypes = properties["graph"].groupChildLinkTypes;
        this.groupCrossLinkTypes = properties["graph"].groupCrossLinkTypes;
      },
    );*/

    eventCommunicationService.splitterMoved$.subscribe(
      () => {
        this.updateViewBox(false);
      }
    );
    eventCommunicationService.treeItemSelected$.subscribe(
      selection => {
        const heldControl = this.d3.event.ctrlKey;
        this.selectNodes([selection], this.clickToAdd || heldControl);
      }
    );
    eventCommunicationService.searchItemSelected$.subscribe(
      selection => {
        this.selectNodes([selection.item], selection.addToSelection);
      }
    );
    eventCommunicationService.resetZoomClicked$.subscribe(
      () => {
        this.resetZoom(true);
      }
    );
    eventCommunicationService.expandAllClicked$.subscribe(
      () => {
        this.expandGraph();
      }
    );
    eventCommunicationService.collapseAllClicked$.subscribe(
      () => {
        this.collapseGraph();
      }
    );
    eventCommunicationService.typesSelected$.subscribe(
      types => {
        this.selectedTypes = types;
        if (this.graph != null) {
          this.makeGraphWithMessage();
        }
      }
    );

  }

  ngOnInit() {
    this.initVariables();
  }

  private initVariables() {
    this.svg = this.d3.select('#graph').select('svg');
    this.viewBox = {};
    this.svgGroup = this.svg.append('g');
    this.groupGraphs = {};
    this.group = this.svgGroup.append('g').attr('id', 'groupGroup').selectAll('.group');
    this.path = this.svgGroup.append('g').attr('id', 'linkGroup').selectAll('.link');
    this.node = this.svgGroup.append('g').attr('id', 'nodeGroup').selectAll('.node');
    this.groupMargin = 16;
    this.nodePadding = 2;
    this.nodeMargin = 8;
    this.selectedNodes = [];
    this.fixedNodes = [];
    this.zoom = this.d3.zoom().on('zoom', () => {
      this.svgGroup.attr('transform', this.d3.event.transform);
      this.d3.select('#contextMenu').style('display', null);

    });
    this.svg.call(this.zoom);
    this.svg.style('cursor', 'default');

    this.status = {progressBarValue : 0, text : ''};
  }

  private initView() {
    this.loadProperties().then(
      () => {
        this.resetSvg();

        this.initGraphs();
        this.setUpLayout();

        // Check MenuButtons
      },
      error => console.log(error)
    )
  }

  private loadProperties(): Promise<any> {

    return new Promise((resolve, reject) => {
      this.selectedProjectService.getProperties()
        .subscribe(
          properties => {
            // if (properties.hasOwnProperty('graph')) {
              this.groupItemType = properties.graph.groupItemType;
              this.subGroupItemType = properties.graph.subGroupItemType;
              this.groupChildLinkTypes = properties.graph.groupChildLinkTypes;
              this.groupCrossLinkTypes = properties.graph.groupCrossLinkTypes;
            /* } else {
               this.groupItemType = '';
              this.subGroupItemType = [];
              this.groupChildLinkTypes = [];
              this.groupCrossLinkTypes = [];
            } */
            resolve();
          },
          error => {
            reject(error);
          }
        );
    });
  }

  private updateGraph() {

    if (this.isHierarchical) {
      this.makeHierarchicalGraph();
    } else {
      this.makeGraphWithMessage();
    }

  }

  private resetSvg() {
    const svg = this.svg;

    if (svg) {
      svg.selectAll('g').remove();

      this.svgGroup = this.svg.append('g');
      this.group = this.svgGroup.append('g').attr('id', 'groupGroup').selectAll('.group');
      this.path = this.svgGroup.append('g').attr('id', 'linkGroup').selectAll('.link');
      this.node = this.svgGroup.append('g').attr('id', 'nodeGroup').selectAll('.node');

    }
  }

  private initGraphs() {
    this.groupGraphs[this.groupItemType] =
      this.graphCanvasHelperService.createGroupGraphInitial(
        this.groupItemType, this.groupChildLinkTypes, this.groupCrossLinkTypes, this.graph);


    this.hierarchicalGraph = {

      elements: [].concat(this.groupGraphs[this.groupItemType].elements),
      links: [].concat(this.groupGraphs[this.groupItemType].links),
      groups: [],
      isHierarchical: true
    };

    this._graph.groups = [];
    this.fullGraph = this.graph;

    // Show nothing at beginning
    this.currentGraph = { elements: [], links: [], groups: [] };
  }

  private setUpLayout () {
    const d3 = this.d3;

    this.layout = cola.d3adaptor(d3);

    this.layout
      .on('tick', () => {
        this.tick();
      })
      .on('start', () => {
        this.setProgressBar(0, 'layouting...');
      })
      .on('end', () => {

        this.fixNodes([]);
        this.setProgressBar(100, 'Layout finished.');
      });

    this.drag = d3.drag().subject(d => {
      return d;
    }).on('start', (d, index, elements) => {
      // close context Menu
      const context = d3.select('#contextMenu');
      context.style('display', null);

      // don't propagate to zoom behaviour
      // d3.event.sourceEvent.stopPropagation();

      d3.select(elements[index]).classed('dragging', true);

    }).on('drag', (d, index, elements) => {
      this.fixNodes([]);

      d['x'] = d3.event.x;
      d['y'] = d3.event.y;

      this.tick();

      d3.select(elements[index])
        .attr('transform', 'translate(' + d['x'] + ',' + d['y'] + ')');

    }).on('end', (d, index, elements) => {
      d3.select(elements[index]).classed('dragging', false);
    });

    d3.select('#contextMenu').call(
      d3.drag().on('drag', function () {
        const cm = d3.select(this);
        const left = Number.parseInt(cm.style('left'));
        const top = Number.parseInt(cm.style('top'));
        cm.style('left', left + d3.event.dx + 'px');
        cm.style('top', top + d3.event.dy + 'px');
      })
    );

    // context menu closing
    ['click', 'wheel'].forEach(type => {
      d3.select('body').on(type, () => {
        d3.select('#contextMenu').style('display', null);
      });
    });

    this.updateViewBox(true);
  }

  private fixNodes(nodes) {

    this.fixedNodes.forEach(n => {

      n.fixed = false;
    });

    nodes.forEach(n => {

      n.fixed = true;
    });
    this.fixedNodes = nodes;
  }

  private tick() {
    const d3 = this.d3;

    const groups = d3.selectAll('#groupGroup').selectAll('g');
    const paths = d3.select('#linkGroup').selectAll('g');
    const nodes = d3.select('#nodeGroup').selectAll('g');

    // avoid node overlapping
    this.layout.avoidOverlaps(this.layoutActive);

    groups.attr('transform', d => {
      return 'translate(' + d['bounds'].x + ',' + d['bounds'].y + ')';
    });

    groups.selectAll('rect').attr('width', d => {
      return d['bounds'].X - d['bounds'].x - 32;
    }).attr('height', d => {
      return d['bounds'].Y - d['bounds'].y - 32;
    });

    //  draw links at node's border
    paths.selectAll('path').attr('d', d => {
      const targetPos = this.calcArrowPosition(d['source'], d['target']);
      const sourcePos = this.calcArrowPosition(d['target'], d['source']);

      return 'M' + sourcePos.x + ',' + sourcePos.y +
        'L' + targetPos.x + ',' + targetPos.y;
    });

    nodes.attr('transform', d => {
      return 'translate(' + d['x'] + ',' + d['y'] + ')';
    });

  }

  private calcArrowPosition(source, target) {

    const pad = 2 - this.nodeMargin,

      dx = target.x - source.x,
      dy = target.y - source.y,

      hw = target.width * 0.5 + pad,
      hh = target.height * 0.5 + pad,

      nx = dx < 0 ? -hh : hh,
      ny = dy < 0 ? hw : -hw,

      prod = dx * nx + dy * ny,

      bx = (dx < 0 ? hw : -hw),
      by = (dy < 0 ? hh : -hh),

      targetX = target.x + (prod > 0 ? bx : by * dx / dy),
      targetY = target.y + (prod > 0 ? bx * dy / dx : by);

    return { x: targetX, y: targetY };
  }

  private updateViewBox(reset) {

    const bBox = () => {
      return this.svg.node().getBoundingClientRect();
    };

    if (reset) {

      this.viewBox.x = 0;
      this.viewBox.y = 0;

    } else {

      this.viewBox.x += (this.viewBox.w - bBox().width) * 0.5;
      this.viewBox.y += (this.viewBox.h - bBox().height) * 0.5;
    }
    this.viewBox.w = bBox().width;
    this.viewBox.h = bBox().height;

    this.svg.attr('viewBox',
      this.viewBox.x + ' ' + this.viewBox.y + ' ' + this.viewBox.w + ' ' + this.viewBox.h);

    this.layout.size([
      this.viewBox.w,
      this.viewBox.h
    ]);
  }

  private selectNodes(nodes, addOrRemove) {

    if (addOrRemove) {

      const unselectedNodes = nodes.filter(n => {

        return this.selectedNodes.indexOf(n) < 0;
      });

      if (unselectedNodes.length) {

        unselectedNodes.forEach(n => {

          this.selectedNodes.push(n);
        });

      } else {

        nodes.forEach(n => {
          // remove node from selection
          this.selectedNodes.splice(this.selectedNodes.indexOf(n), 1);
        });
      }
    } else {

      this.resetZoom(false);
      this.selectedNodes = nodes;
    }

    this.eventCommunicationService.selectCanvasItems(this.selectedNodes);

    this.makeGraphWithMessage();
  }

  private resetZoom(resetScale: boolean) {
    const d3 = this.d3;
    const zoom = this.zoom;

    let transformation = d3.zoomIdentity;

    if (!resetScale) {

      // center view using current scale
      const currentTransform = d3.zoomTransform(this.svg.node());

      const dx = -this.viewBox.w * (currentTransform.k - 1) * 0.5;
      const dy = -this.viewBox.h * (currentTransform.k - 1) * 0.5;

      transformation = transformation.translate(dx, dy);
    }

    this.svgGroup.call(zoom.transform, transformation);
    this.svg.transition()
      .duration(750)
      .call(zoom.transform, d3.zoomIdentity);

  }

  private makeGraphWithMessage() {
    this.setProgressBar(0, 'Making graph...');

    this.makeGraph();
  }

  /*
   make and show flat Graph
   */
  private makeGraph() {

    // exclude unselected types
    const selectedTypes = this.selectedTypes;

    const filteredNodes = this.fullGraph.elements.filter((e) => {
      return selectedTypes.indexOf(e.type) > -1;
    });

    const filteredLinks = this.fullGraph.links.filter((l) => {
      return filteredNodes.indexOf(l.source) > -1 && filteredNodes.indexOf(l.target) > -1;
    });

    let graph = { elements: filteredNodes, links: filteredLinks };

    // show only selected filtered nodes and their neighbours
    graph = GraphCanvasHelperService.closedNeighbourhood(graph, this.selectedNodes.filter(n => {
      return selectedTypes.indexOf(n.type) > -1;
    }), this.allLinks);

    this.currentGraph = graph;
    this.makeGraphElements();
  }

  private makeGraphElements() {
    const graph = this.currentGraph;

    Promise.all([
      this.makeNodeElements(graph),
      this.makeGroupElements(graph),
      this.makePathElements(graph)
    ]).then(
      responses => {
        this.setProgressBar(90);
        const nodeGroup = responses[0];

        this.layout.avoidOverlaps(this.nodeCollision || graph.groups.length);
        this.layout.nodes(graph.elements);
        this.layout.links(graph.links);

        if (graph.groups && graph.groups.length) {
          this.layout.groups(graph.groups);
        } else {
          this.layout.groups([]);
        }

        this.setProgressBar(100);

        this.startLayout(graph);
        this.addDragBehaviour(nodeGroup, this.layout, this.drag, this.layoutActive);
      }
    );

  }

  private makeNodeElements(graph): Promise<any> {
    return new Promise((resolve) =>  {
      const d3 = this.d3;

      this.node = d3.select('#nodeGroup').selectAll('g').data(graph.elements, d => {
        return GraphCanvasHelperService.getNodeId(d);
      });


      this.node.exit().remove();

      const nodeGroup = this.node.enter().append('g')
        .classed('node', true)
        .on('mousedown.zoom', () => {
          // allow dragging of nodes without panning the whole graph
          d3.event.stopPropagation();
        });

      this.node = nodeGroup.merge(this.node);

      const text = nodeGroup.append('text').classed('nodeCaption', true)
        .text(d => {
          if (d.isGroup && graph.isHierarchical) {
            return GraphHelperService.getNodeName(d) + ' [+]';
          }
          return GraphHelperService.getNodeName(d);

        });

      graph.elements.forEach((e, i) => {
        if (!e.width) {
          e.width = text._groups[0][i].getBBox().width + 2 * this.nodePadding + 2 * this.nodeMargin;
          e.height = text._groups[0][i].getBBox().height + 2 * this.nodePadding + 2 * this.nodeMargin;
        }
      });

      const rect = nodeGroup.insert('rect', 'text')
        .style('fill', d => {
          return this.graphCommunicationService.nodeTypes[d.type];
        })
        .attr('x', d => {
          return -d.width * 0.5 + this.nodeMargin;
        })
        .attr('y', d => {
          return -d.height * 0.5 + this.nodeMargin;
        })
        .attr('width', d => {
          return d.width - 2 * this.nodeMargin;
        })
        .attr('height', d => {
          return d.height - 2 * this.nodeMargin;
        });


      d3.selectAll('.selected').classed('selected', false);

      d3.selectAll('.node').filter(n => {
        return this.selectedNodes.indexOf(n) > -1;
      }).classed('selected', true);

      d3.selectAll('.treeNodeCaption').filter(n => {
        return this.selectedNodes.indexOf(n) > -1;
      }).classed('selected', true);



      nodeGroup.on('click', d => {

        // ignore if dragging
        if (!d3.event.defaultPrevented) {

          if (d.isGroup && graph.isHierarchical) {

            this.openGroup(graph, d);

          } else {

            if (graph.isHierarchical) {
              d = this.fullGraph.elements.find(e => {
                return GraphHelperService.elementEquals(d, e);
              });
            }

            const clickToAdd = this.clickToAdd ?
              !d3.event.ctrlKey : d3.event.ctrlKey;

            if (clickToAdd && this.selectedNodes.indexOf(d) < 0) {

              this.fixNodes([d]);
            }
            this.selectNodes([d], clickToAdd);
          }
        }
      });

      nodeGroup.on('contextmenu', d => {
        const contextMenu = d3.select('#contextMenu').style('display', 'block');
        const menu = GraphCanvasHelperService.createContextMenu(contextMenu);
        const li = menu.append('ul').append('li');

        li.append('span').style('background',
          this.graphCommunicationService.nodeTypes[d.type]).text(GraphHelperService.getNodeName(d));
        li.append('span').text(' : ' + d.type + ' {');
        GraphCanvasHelperService.appendProperties(li, d.attributes);
        li.append('span').text('}');
        GraphCanvasHelperService.showContextMenu(menu, d3.event, d3.mouse(document.body));
      });

      resolve(nodeGroup);

    });

  }

  private makeGroupElements(graph: GraphData) {
    return new Promise((resolve) => {
      const d3 = this.d3;

      this.group = d3.select('#groupGroup').selectAll('g').data(graph.groups, d => {
        return 'group' + d.id;
      });

      this.group.exit().remove();

      const groupGroup = this.group.enter().append('g').classed('group', true);

      this.group = groupGroup.merge(this.group);

      groupGroup.append('rect')
        .attr('x', this.groupMargin).attr('y', this.groupMargin)
        .style('stroke', 'black')
        .style('fill', d => {
          return this.graphCommunicationService.nodeTypes[d.type];
        });

      // let groupCaption = groupGroup.append('text').html(function(d) {
      groupGroup.append('text').html(function(d) {
        return d.name + ' [&minus;]';
      }).on('click', d => {
        this.closeGroup(graph, d);
      }).style('cursor', 'pointer')
        .attr('x', this.groupMargin + this.nodePadding).attr('y', this.groupMargin + this.nodePadding + 10);

      resolve();
    });

  }

  private makePathElements(graph) {
    return new Promise((resolve) => {
      const d3 = this.d3;

      this.path = d3.select('#linkGroup').selectAll('g').data(graph.links, d => {
        return GraphCanvasHelperService.getNodeId(d['source']) + '>' + GraphCanvasHelperService.getNodeId(d['target']);
      });

      this.path.exit().remove();


      const linkGroup = this.path.enter().append('g').classed('link', true);

      this.path = linkGroup.merge(this.path);

      linkGroup.append('svg:path')
        .attr('class', d => {

        if (GraphHelperService.isEmpty(d.backwardLinks)) {
          return 'linkVisual';
        } else {
          return 'linkVisual dual';
        }
      });
      linkGroup.append('svg:path').attr('class', 'linkInteract');

      linkGroup.on('click', d => {
        const source = d.source;
        const target = d.target;

        if (graph.isHierarchical) {
          // Dont switch from hierarchy to flat on link press
        } else {
          // ignore if dragging
          if (!d3.event.defaultPrevented) {
            if (d3.event.ctrlKey) {
              this.fixNodes([source, target].filter(n => {

                //  set previous position for fixing
                n.px = n.x;
                n.py = n.y;

                return this.selectedNodes.indexOf(n) < 0;
              }));
            }

            this.selectNodes([source, target], d3.event.ctrlKey);
          }
        }
      });

      linkGroup.on('contextmenu', d => {

        const contextMenu = d3.select('#contextMenu').style('display', 'block');
        const menu = GraphCanvasHelperService.createContextMenu(contextMenu),
          list = menu.append('ul');

        this.graphCanvasHelperService.appendLinkDescription(list, d.forwardLinks, d);
        if (!GraphHelperService.isEmpty(d.backwardLinks)) {
          this.graphCanvasHelperService.appendLinkDescription(list, d.backwardLinks, d);
        }
        GraphCanvasHelperService.showContextMenu(menu, d3.event, d3.mouse(document.body));
      });

      resolve();
    });
  }


  private startLayout(graph) {

    this.updateViewBox(true);

    const logN = Math.log2(graph.elements.length);

    let preTicks = (graph.elements.length / 100) + (graph.groups.length * 2);
    preTicks = Math.max(preTicks, 5);
    preTicks = Math.min(preTicks, 50);

    if (graph.links.length) {
      this.layout.linkDistance(logN * 64);
    }


    this.setProgressBar(0, 'precalculating layout...');
    this.updateProgressBar(preTicks * 100);
    setTimeout(() => {
      this.layout.start(preTicks, preTicks, preTicks);
      this.setProgressBar(0, 'layouting...');

      // stop layout when running too long
      const stopLayout = () => {
        this.layout.stop();
      };

      let layoutTimeFactor = 500;
      if (graph.groups.length) {

        layoutTimeFactor *= 2;
      }

      const layoutTime = Math.max(logN * layoutTimeFactor, 4000);

      this.updateProgressBar(layoutTime);

      window.clearTimeout(this.stopLayoutTimeout);
      this.stopLayoutTimeout = window.setTimeout(stopLayout, layoutTime);
    }, 0);


  }

  private addDragBehaviour(nodes, layout, drag, layoutOnDrag) {
    if (layoutOnDrag) {
      nodes.call(layout.drag());
    } else {
      nodes.call(drag);
    }
  }

  /*
   make and show hierarchical graph
   */
  private makeHierarchicalGraph () {
    GraphCanvasHelperService.reOrderGraph(this.hierarchicalGraph);
    GraphCanvasHelperService.reIndexGroups(this.hierarchicalGraph);
    this.currentGraph = this.hierarchicalGraph;
    this.makeGraphElements();
  }

  private openGroup(graph, group) {
    this.expandGroupIntern(graph, group);

    graph.links = this.graphCanvasHelperService.createGraphLinks(graph.elements, this.groupCrossLinkTypes, this.fullGraph);
    this.graphCanvasHelperService.mapLinks(graph);

    GraphCanvasHelperService.reOrderGraph(graph);

    this.makeGraphElements();

  }

  private closeGroup(graph, group) {
    this.closeSubGroup(graph, group);

    graph.links = this.graphCanvasHelperService.createGraphLinks(graph.elements, this.groupCrossLinkTypes, this.fullGraph);
    this.graphCanvasHelperService.mapLinks(graph);

    GraphCanvasHelperService.reOrderGraph(graph);
    this.makeGraphElements();
  }

  private closeSubGroup(graph, group) {

    const groupTree = this.graphTreeService.getSubTree(group.id);
    const children = groupTree.children;
    const element = groupTree.element;

    children.forEach(ch => {
      const subElement = ch.element;

      if (subElement.isGroup) {
        const subGroup = graph.groups.find(g => {
          return GraphHelperService.elementEquals(g, subElement);
        });
        if (typeof subGroup !== 'undefined') {
          this.closeSubGroup(graph, subGroup);
        }

      }

      const elementInGraph = graph.elements.find(e => {
        return GraphHelperService.elementEquals(subElement, e);
      });

      graph.elements.splice(graph.elements.indexOf(elementInGraph), 1);
    });

    element.isGroup = true;

    if (groupTree.parent != null) {
      element.myParent = graph.groups.find(g => {
        return GraphHelperService.elementEquals(g, groupTree.parent);
      });
    } else {
      element.myParent = null;
    }

    graph.elements.push(element);


    const elementInGroups = graph.groups.find(e => {
      return element.id === e.id;
    });
    graph.groups.splice(graph.groups.indexOf(elementInGroups), 1);

    const topGroup = group.myParent;
    if (topGroup != null) {

      const elementInTopGroup = topGroup.groups.find(e => {
        return element.id === e.id;
      });

      topGroup.groups.splice(topGroup.groups.indexOf(elementInTopGroup), 1);
      topGroup.leaves.push(element);

    }


  }

  private expandGroupIntern(graph, group) {
    GraphCanvasHelperService.restoreLinks(graph);

    //  remove closed-group element
    const index = graph.elements.indexOf(group);
    graph.elements.splice(index, 1);

    GraphCanvasHelperService.reIndexGroups(graph);

    const children = this.graphTreeService.getSubTree(group.id).children;

    const newGroup = {
      id: group.id,
      name: GraphHelperService.getNodeName(group),
      type: group.type,
      myParent: group.myParent,
      padding: 32,
      leaves: [],
      inHierarchyGraph: true
    };

    children.forEach(ch => {
      const element = ch.element;

      let isGroupElement = false;
      let filter = false;
      if (typeof this.subGroupItemType !== 'undefined' && this.subGroupItemType != null && this.subGroupItemType.length > 0) {
        for (const type of this.subGroupItemType) {
          if (element.type === type) {
            filter = true;
          }
        }
      }

      if (element.type === this.groupItemType || filter) {
        isGroupElement = true;
      }

      const hasChildren = ch.children.length > 0;

      element.myParent = newGroup;
      element.isGroup = isGroupElement && hasChildren;

      graph.elements.push(element);
      newGroup.leaves.push(element);
    });

    graph.groups.push(newGroup);

    let parentIndex;
    if (newGroup.myParent === null) {
      parentIndex = -1;
      // graph.groups.push(newGroup);
    } else {
      const topGroup = newGroup.myParent;

      parentIndex = graph.groups.indexOf(topGroup);

      if (typeof topGroup.groups === 'undefined') {
        topGroup.groups = [];
      }
      topGroup.groups.push(newGroup);
      topGroup.leaves.splice(topGroup.leaves.indexOf(-1), 1);

    }

  }

  private expandGraph() {
    const graph = this.currentGraph;
    let groups = graph.elements.filter(e => {
      return e.isGroup;
    });

    while (groups.length > 0) {
      const group = groups.shift(); // Get first element
      this.expandGroupIntern(graph, group);

      groups = graph.elements.filter(e => {
        return e.isGroup;
      });
    }

    graph.links = this.graphCanvasHelperService.createGraphLinks(graph.elements, this.groupCrossLinkTypes, this.fullGraph);
    this.graphCanvasHelperService.mapLinks(graph);

    GraphCanvasHelperService.reOrderGraph(graph);

    this.currentGraph = graph;
    this.makeGraphElements();

  }

  private collapseGraph() {
    this.currentGraph = { elements: [], links: [], groups: [] };
    this.makeGraph();
    this.hierarchicalGraph = {
      elements: [].concat(this.groupGraphs[this.groupItemType].elements),
      links: [].concat(this.groupGraphs[this.groupItemType].links),
      groups: [],
      isHierarchical: true
    };
    this.makeHierarchicalGraph();
  }

  private updateProgressBar(layoutTime: number) {
    const steps = 5;
    const delay = layoutTime / (100 / steps);

    this.status.progressBarValue += steps;
    if (this.status.progressBarValue < 90) {
      setTimeout(() => {
        this.updateProgressBar(layoutTime);
      }, delay);
    }
  }

  private setProgressBar(value: number, text?: string) {
    this.status.progressBarValue = value;
    if (text) {
      this.status.text = text;
    }
  }

}



