import {Project} from './project';
/**
 * Created by johannes on 04.08.17.
 */
export class GlobalProperties {
  readonly defaultProject: string;
  readonly neo4jServer: string;

  public static convertJSONToObject(json: JSON): GlobalProperties {
    let defaultProject = '';
    let neo4j = '/neo4j';

    if (json['defaultProject'] && typeof json['defaultProject'] === 'string') {
      defaultProject = json['defaultProject'];
    }
    if (json['neo4jServer']) {
      neo4j = json['neo4jServer'];
    }

    return new GlobalProperties(defaultProject, neo4j);
  }

  constructor(defaultProject: string, neo4jServer: string) {
    this.defaultProject = defaultProject;
    this.neo4jServer = neo4jServer
  }
}



