import {NgModule, ModuleWithProviders} from '@angular/core';
import { CommonModule } from '@angular/common';
import { GraphComponent } from './graph/graph.component';

import { GraphRoutingModule } from './graph-routing.module';
import {SelectedProjectService} from '../selected-project.service';
import {SelectedOdService} from '../selected-od.service';
import {D3Service} from 'd3-ng2-service';
import { GraphTreeComponent } from './graph-tree/graph-tree.component';
import { GraphMainComponent } from './graph-main/graph-main.component';
import {GraphHelperService} from './helper/graph-helper.service';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { TabContentComponent } from './graph-tree/tab-content/tab-content.component';
import {EventCommunicationService} from './event-communication.service';
import { GraphCanvasComponent } from './graph-main/graph-canvas/graph-canvas.component';
import {FormsModule} from '@angular/forms';
import {GraphCommunicationService} from './graph-communication.service';
import { GraphSearchComponent } from './graph-main/graph-search/graph-search.component';
import { GraphLegendComponent } from './graph-main/graph-legend/graph-legend.component';
import {GraphCanvasHelperService} from './helper/graph-canvas-helper.service';
import {GraphTreeService} from './helper/graph-tree.service';
import {ColorService} from '../color.service';

@NgModule({
  imports: [
    CommonModule,
    GraphRoutingModule,
    NgbModule,
    FormsModule
  ],
  declarations: [
    GraphComponent,
    GraphTreeComponent,
    GraphMainComponent,
    TabContentComponent,
    GraphCanvasComponent,
    GraphSearchComponent,
    GraphLegendComponent
  ],
  providers: [
    GraphHelperService,
    EventCommunicationService,
    GraphCommunicationService,
    GraphCanvasHelperService,
    GraphTreeService
  ]
})
export class GraphModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: GraphModule,
      providers: [
        SelectedProjectService,
        SelectedOdService,
        D3Service,
        ColorService
      ]
    };
  }
}



