import { Injectable } from '@angular/core';
import {D3, D3Service} from 'd3-ng2-service';
import * as ColorBrewer from 'colorbrewer';
import {BehaviorSubject} from 'rxjs/Rx';

@Injectable()
export class ColorService {

  private d3: D3;

  private _domain = new BehaviorSubject<any[]>([]);

  public domain$ = this._domain.asObservable();


  constructor(d3Service: D3Service) {
    this.d3 = d3Service.getD3();
    const domain = ColorBrewer.Set3[12];

    /*
    let d3 = this.d3;
    let color : ScaleOrdinal<number, string> = d3.scaleOrdinal<number, string>(d3.schemeCategory20   );
    //return color;

    for (let i = 0; i < 20; i++) {
      domain.push(color(i).toString());
    }
    domain = domain.filter((c,i) => {
      return i % 2 == 1;
    });
    */

    this._domain.next(domain);
  }

  public getColorScheme(): any[] {
    return this._domain.getValue();
  }

  public setColorScheme(number: number, colorCount: number) {
    let domain;
    let color = 8;
    if (colorCount) {
      color = colorCount;
    }
    switch (number) {
      case 1 :
        if (color > 8) { color = 8; }
        domain = ColorBrewer.Accent[color];
        break;
      case 2 :
        if (color > 8) { color = 8; }
        domain = ColorBrewer.Dark2[color];
        break;
      case 3 :
        if (color > 9) { color = 9; }
        domain = ColorBrewer.Pastel1[color];
        break;
      case 4 :
        if (color > 8) { color = 8; }
        domain = ColorBrewer.Pastel2[color];
        break;
      case 5 :
        if (color > 9) { color = 9; }
        domain = ColorBrewer.Set1[color];
        break;
      case 6 :
        if (color > 8) { color = 8; }
        domain = ColorBrewer.Set2[color];
        break;
      case 7 :
        if (color > 12) { color = 12; }
        domain = ColorBrewer.Set3[color];
        break;
    }
    this._domain.next(domain);

  }

}



