import {MetricDataItem} from './metric-data-item';

export class Metric {
  readonly name: string;
  readonly data: MetricDataItem[];

  private _description: string;

  set description(description: string) {
    this._description = description;
  }

  get description(): string {
    return this._description;
  }

  constructor(name: string, data: MetricDataItem[]) {
    this.name = name;
    this.data = data;
  }



}



