import {Component, OnInit, ViewEncapsulation, ViewChild, AfterViewInit} from '@angular/core';
import {SelectedProjectService} from '../../selected-project.service';
import {SelectedOdService} from '../../selected-od.service';
import {TreeComponent as LibTreeComponent} from 'angular-tree-component';
import {Objectdiagram} from '../../helper/objectdiagram';

@Component({
  selector: 'app-tree',
  templateUrl: './tree.component.html',
  styleUrls: ['./tree.component.css'],
  // For Custom CSS in Tree
  encapsulation: ViewEncapsulation.None,
})
export class TreeComponent implements OnInit, AfterViewInit {

  @ViewChild(LibTreeComponent)
  private tree: LibTreeComponent;

  private nodes: Array<JSON>;
  private selectedElement;

  constructor(
    private selectedProjectService: SelectedProjectService,
    private selectedOdService: SelectedOdService
  ) {
    this.selectedElement = '';

    selectedProjectService.selectedProject$.subscribe(
      selectedProject => {
        this.selectedProjectChanged(selectedProject);
      }
    );

    selectedProjectService.folderStructure$.subscribe(
      folderStructure => {
        this.nodes = folderStructure;
      }
    );

    selectedOdService.selectedOd$.subscribe(
      selectedOd => {
        if (this.selectedElement.path !== selectedOd.path) {
          // Timeout to give the TreeView Lib Time to Initialize
          setTimeout(() => this.selectedOdChanged(selectedOd), 0);
        }
      }
    );


  }

  ngOnInit() {
  }

  ngAfterViewInit() {
    const project = this.selectedProjectService.getSelectedProject().getValue();
    if (project.name !== '') {
      this.selectedProjectChanged(project);

      const od = this.selectedOdService.getSelectedOd();
      if (od != null && this.nodes != null) {
        // Timeout to give the TreeView Lib Time to Initialize
        setTimeout(() => this.selectedOdChanged(od), 0);
      }

    }
  }


  selectElement ($event) {
    // Is not a folder
    const element = $event.node.data;
    if (!element.children) {
      this.selectedElement = element;
      const od = this.selectedProjectService.getOdFromPath(element.path);
      if (od) {
        this.selectedOdService.setSelectedOd(od);
      }
    }

  }

  private selectedProjectChanged(newProject) {
    this.selectedElement = '';
  }

  private selectedOdChanged(newOd: Objectdiagram) {
    this.activateNode(newOd, this.nodes);
  }

  private activateNode(newOd: Objectdiagram, tree: JSON[]) {
    for (const node of tree) {
      if (node['path'] === newOd.path) {

        const treeNode = this.tree.treeModel.getNodeById(node['id']);
        setTimeout(() => {
          treeNode.setActiveAndVisible();
          return;
        }, 0);


      } else if (newOd.path.startsWith(node['path'])) {
        if (node.hasOwnProperty('children')) {
          const children = node['children'];
          this.activateNode(newOd, children);
          return;
        }
      }
    }

  }


}



