import { Injectable } from '@angular/core';
import {GraphHelperService} from './graph-helper.service';

@Injectable()
export class GraphTreeService {

  private tree = {element : null, children: [], parent : null};
  private topTypeTree = {element : null, children: [], parent : null};
  private allElementsInTree = [this.tree];
  private unsorted = [];
  private topType;
  private map = [];

  private static getSubTreeOfElement(element, searchTree) {
    if (element === null) {
      return null;
    }
    return GraphTreeService.getSubTreeOfElementWithId(element.id, searchTree);
  }

  private static getSubTreeOfElementWithId(elementId, searchTree) {
    let list = [searchTree];
    while (list.length > 0) {
      const i = list[0];

      if (i.element !== null && i.element.id === elementId) {
        return i;
      } else {
        list.splice(list.indexOf(i), 1);
        if (typeof i.children !== 'undefined') {
          list = list.concat(i.children);
        }
      }
    }
    return null;
  }

  constructor() { }

  public createTree() {
    const rootElements = this.allElementsInTree.filter(e => {
      return e.parent === null;
    });
    rootElements.shift();
    this.tree.children = rootElements;

    this.topTypeTree.children = rootElements.filter(e => {
      return e.element.type === this.topType;
    });

    this.tree.children.forEach(c => {
      if (c.element.type === this.topType) {
        this.createSubMap(c, []);
      }
    });
  }

  private createSubMap(subTree, ancestors) {
    const mapElement = {element: subTree.element, ancestors: ancestors};
  this.map.push(mapElement);
  subTree.children.forEach(c => {
    this.createSubMap(c, ancestors.concat(subTree.element));
  });

}

  public push(element, parent) {
    const newElement = {element : element, children: [], parent : parent};

    // Check if new Element is Parent of unsorted elements
    this.unsorted.forEach(unEl => {
      if (GraphHelperService.elementEquals(unEl.parent, newElement.element)) {
        // if (unEl.parent === newElement.element) {
        unEl.parent = newElement.element;
        newElement.children.push(unEl);
        this.unsorted.splice(this.unsorted.indexOf(newElement.element), 1);

      }
    });

    // Push to FlatTree
    const i = this.allElementsInTree.find(e => {
      return GraphHelperService.elementEquals(parent, e.element);
    });
    if (typeof i !== 'undefined') {
      this.allElementsInTree.push(newElement);
      i.children.push(newElement);
      return;
    }

    this.unsorted.push(newElement);

  }

  public contains(element) {
    return (GraphTreeService.getSubTreeOfElement(element, this.tree) !== null);
  }

  /* public isDescendant(element, ancestor) {
    const mapElement = this.map.find(e => {
      return GraphHelperService.elementEquals(e.element, element);
    });
    if (typeof mapElement === 'undefined') {
      return false;
    }
    return mapElement.ancestors.some(a => {
      return GraphHelperService.elementEquals(a, ancestor);
    });

  }*/

  public getAncestors(element) {
    return this.getInnerAncestors(element, this.topTypeTree);
  }

  private getInnerAncestors(element, tree) {
    // I am the subroot
    if (GraphHelperService.elementEquals(element, tree.element)) {
      return [tree.element];
    }

    if (typeof tree.children === 'undefined' || tree.children.length === 0) {
      return undefined;
    }

    // Search subtrees;
    for (let i = 0; i < tree.children.length; i++) {
      // tree.children.forEach(c => {
      const ancestors = this.getInnerAncestors(element, tree.children[i]);
      if (typeof ancestors !== 'undefined') {
        ancestors.push(tree.element);
        return ancestors;
      }
    }
  }

  public getSubTree(elementId) {
    return GraphTreeService.getSubTreeOfElementWithId(elementId, this.tree);
  }

  public setTopType(type) {
  this.topType = type;
  }

  public reset() {
    this.tree = {element : null, children: [], parent : null};
    this.allElementsInTree = [this.tree];
    this.map = [];
    this.unsorted = [];
    this.topType = null;
  }

  public getTree() {
    return this.tree;
  }

}



