import { Injectable } from '@angular/core';
import {Subject} from 'rxjs/Rx';
import {SearchEvent} from './helper/SearchEvent';

@Injectable()
export class EventCommunicationService {

  private _splitterMoved = new Subject<any>();

  private _treeItemSelected = new Subject<any>();
  private _canvasItemsSelected = new Subject<any[]>();
  private _searchItemSelected = new Subject<SearchEvent>();

  private _itemSelected = new Subject<any>();

  private _resetZoomClicked = new Subject<any>();
  private _expandAllClicked = new Subject<any>();
  private _collapseAllClicked = new Subject<any>();
  private _typesSelected = new Subject<string[]>();

  public splitterMoved$ = this._splitterMoved.asObservable();

  public treeItemSelected$ = this._treeItemSelected.asObservable();
  public canvasItemsSelected$ = this._canvasItemsSelected.asObservable();
  public searchItemSelected$ = this._searchItemSelected.asObservable();

  public itemSelected$ = this._itemSelected.asObservable();

  public resetZoomClicked$ = this._resetZoomClicked.asObservable();
  public expandAllClicked$ = this._expandAllClicked.asObservable();
  public collapseAllClicked$ = this._collapseAllClicked.asObservable();
  public typesSelected$ = this._typesSelected.asObservable();


  constructor() {}

  public splitterMoved() {
    this._splitterMoved.next();
  }

  public selectTreeItem (treeItem: any) {
    this._treeItemSelected.next(treeItem);
    this.itemSelected();
  }

  // Is also called, if tree select is called
  public selectCanvasItems (canvasItems: any[]) {
    this._canvasItemsSelected.next(canvasItems);
    this.itemSelected();
  }

  public selectSearchItem(searchItem: SearchEvent) {
    this._searchItemSelected.next(searchItem);
    this.itemSelected();
  }

  private itemSelected() {
    this._itemSelected.next();
  }

  public resetZoomClicked() {
    this._resetZoomClicked.next();
  }

  public expandAllClicked() {
    this._expandAllClicked.next();
  }

  public collapseAllClicked() {
    this._collapseAllClicked.next();
  }

  public selectTypes(types: string[]) {
    this._typesSelected.next(types);
  }



}



