import {NgModule, ModuleWithProviders} from '@angular/core';
import { CommonModule } from '@angular/common';
import { TreeModule } from 'angular-tree-component';

import { TextualComponent } from './textual/textual.component';

import { TextualRoutingModule } from './textual-routing.module';
import { TreeComponent } from './tree/tree.component';
import { ViewComponent } from './view/view.component';
import {SelectedProjectService} from '../selected-project.service';
import {SelectedOdService} from '../selected-od.service';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';


@NgModule({
  imports: [
    CommonModule,
    TextualRoutingModule,
    TreeModule,
    NgbModule
  ],
  declarations: [TextualComponent, TreeComponent, ViewComponent]
})
export class TextualModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: TextualModule,
      providers: [SelectedProjectService, SelectedOdService]
    };
  }
}



