export class Objectdiagram {
  private _name: string;
  private _path: string;
  private _jsonPath: string;

  constructor(name: string, path: string, jsonPath: string) {
    this._name = name;
    this._path = path;
    this._jsonPath = jsonPath;
  }

  get name(): string {
    return this._name;
  }

  get path(): string {
    return this._path;
  }

  get jsonPath(): string {
    return this._jsonPath;
  }

}



