export class MainProperties {
  readonly defaultFile: string;
  readonly showAllDropdownElements: boolean;
  readonly dropdownElements: string[];

  constructor(defaultFile: string, showAllDropdownElements: boolean= true, dropdownElements?: string[]) {
    this.defaultFile = defaultFile;
    this.showAllDropdownElements = showAllDropdownElements;
    this.dropdownElements = dropdownElements;

  }

}



