import {Component, OnInit, AfterViewInit, OnDestroy} from '@angular/core';
import {Metric} from '../helper/metric';
import {SelectedProjectService} from '../../selected-project.service';
import {SelectedOdService} from '../../selected-od.service';
import {Table} from '../helper/table';
import {MetricGraphicalProp} from '../../helper/metricGraphicalProp';
import {Properties} from '../../helper/properties';

@Component({
  selector: 'app-metric',
  templateUrl: './metric.component.html',
  styleUrls: ['./metric.component.css']
})
export class MetricComponent implements OnInit, AfterViewInit, OnDestroy {

  private metrics: string[] ;
  private selectedMetric: string;
  private showOnlyHighestResults: boolean;
  private resultCount: number;
  private properties: MetricGraphicalProp[];

  private metricsInDropdown: string[];
  private coloring: any;

  constructor(
    private selectedProjectService: SelectedProjectService,
    private selectedOdService: SelectedOdService
  ) {
    this.showOnlyHighestResults = true;
    this.resultCount = 5;
    this.selectedMetric = 'Metric';
    this.coloring = {active : false, min : 0, max : 0};

    // TODO Async
    selectedProjectService.properties$.subscribe(
      properties => {
        this.initProperties(properties);
      }
    );
  }

  ngOnInit() {
    this.selectedOdService.enableOdDropdown(false);
  }

  ngAfterViewInit() {
    const project = this.selectedProjectService.getSelectedProject().getValue();
    if (project.name !== '') {
      this.selectedProjectService.getProperties().subscribe(
        properties => {
          this.initProperties(properties);
        }
      )
    }
  }

  ngOnDestroy() {
    this.selectedOdService.enableOdDropdown(true);
  }


  private initProperties(properties: Properties) {
    // if ( properties['metrics']) {
      this.properties = properties.metrics.graphical;
      this.metrics = [];
      for (const prop of this.properties ) {
        this.metrics.push(prop.name)
      }
      setTimeout(() => {
        const usedOd = this.selectedProjectService.getOdFromPath(properties.metrics.usedOd);
        this.selectedOdService.setSelectedOd(usedOd);
      }, 0);

    // }
  }

  private selectMetricGraphical(metric: any) {
    this.selectedMetric = metric;

    if (this.properties) {
      let min;
      let max;
      let active;
      const prop = this.properties.find(m => {
        return m.name === metric;
      });
      if (prop != null && prop.coloring) {
        min = prop.coloringMin;
        max = prop.coloringMax;
        active = true;
      } else {
        active = false;
      }
      this.coloring = {active: active, min : min, max : max};
    }

  }

  private resultCountChanged(event: number) {

    // Avoid double Events
    if (event !== this.resultCount) {
      this.resultCount = event;
    }

  }

  private subTableChanged(event: Table) {

    setTimeout(() => {
      this.metricsInDropdown = this.metrics.filter(e => {
        return event.metrics.indexOf(e) > -1;
      });
      if (this.metricsInDropdown.indexOf(this.selectedMetric) === -1) {
        this.selectedMetric = 'Metric';
      }
    }, 0);

  }
}



