export class GraphData {
    private _elements: any[];
    private _links: any[];
    private _groups: any[];

    constructor(json?: JSON) {
      if (json) {
        this._elements = json['elements'];
        this._links = json['links'];
      } else {
        this._elements = [];
        this._links = [];
      }
        this._groups = [];
    }


    get elements (): any[] {
        return this._elements;
    }

    set elements(elements: any[]) {
        this._elements = elements;
    }

    get links(): any[] {
        return this._links;
    }

    set links(links: any[]) {
        this._links = links;
    }

    get groups (): any[] {
      return this._groups;
    }

    set groups(groups: any[]) {
      this._groups = groups;
    }

}



