import {Response} from '@angular/http';
import {Observable} from 'rxjs/Rx';
/**
 * Created by johannes on 27.03.17.
 */
export abstract class HttpService {
  protected static extractData(res: Response) {
    const body = res.json();
    return body || {};
  }

  protected static handleError(error: Response | any) {
    // taken from AngularIO Doc
    // In a real world app, you might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }

  protected static handleErrorWith404Acceptance(error: Response | any) {
    if (error.status === 404) {
      return Observable.of(null);
    }
    return HttpService.handleError(error);
  }

}



