import {GraphTab} from './graphTab';
/**
 * Created by johannes on 24.07.17.
 */
export class GraphProperties {
  readonly groupItemType: string;
  readonly subGroupItemType: string[];
  readonly groupChildLinkTypes: string[];
  readonly groupCrossLinkTypes: string[];

  readonly tabs: GraphTab[];

  constructor(groupItemType: string, subGroupItemType: string[],
              groupChildLinkTypes: string[], groupCrossLinkTypes: string[], tabs: GraphTab[]
  ) {
  this.groupItemType = groupItemType;
  this.subGroupItemType = subGroupItemType;
  this.groupChildLinkTypes = groupChildLinkTypes;
  this.groupCrossLinkTypes = groupCrossLinkTypes;
  this.tabs = tabs;
  }

}



