import {SubGroups} from './subGroups';
import {MetricGraphicalProp} from './metricGraphicalProp';
import {Description} from './description';
/**
 * Created by johannes on 24.07.17.
 */
export class MetricProperties {
  readonly graphical: MetricGraphicalProp[];
  readonly descriptions: Description[];
  readonly subGroups: SubGroups[];
  readonly usedOd: string;

  constructor(graphical: MetricGraphicalProp[] = [],
              descriptions: Description[] = [],
              subGroups: SubGroups[] = [],
              usedOd: string = '') {
    this.graphical = graphical;
    this.descriptions = descriptions;
    this.subGroups = subGroups;
    this.usedOd = usedOd;
  }
}



