import { Injectable } from '@angular/core';
import {Http, RequestOptions, Headers} from "@angular/http";
import {Observable} from "rxjs";
import {HttpService} from "../helper/httpService";
import {AllProjectsService} from "../all-projects.service";

@Injectable()
export class CypherDataService extends HttpService {

  private defaultOptions: RequestOptions;

  constructor(private http: Http, private allProjectService: AllProjectsService) {
    super();

    const headers = new Headers();
    headers.append('Authorization', 'Basic ' + btoa(`neo4j:test`));
    headers.append('Content-Type', 'application/json');
    this.defaultOptions = new RequestOptions({
      headers: headers
    });

  }

  public executeStatement(statement: string): Promise<any> {
    let statements = statement.split(";");
    statements = statements.map(s => {
      return s + ';';
    });
    statements.pop();

    let statementList = [];
    statements.forEach(st => {
      statementList.push({
        "statement" : st,
        "resultDataContents" : [ "row", "graph"]
      })
    });


    const body = {
      "statements" : statementList
    };

    return new Promise((resolve) => {

      this.allProjectService.getGlobalProperties().subscribe(
        global => {
          const path = global.neo4jServer + '/db/data/transaction/commit';
          this.getData(path, body).subscribe(
            res => {
              resolve(res)
            }
          )
        }
      )

    });


  }

  private getData(path, body): Observable<any> {
    return this.http.post(path, body, this.defaultOptions)
      .map(CypherDataService.extractData)
      .catch(CypherDataService.handleError);
  }

  public getGraph(): Promise<any> {
    return new Promise(resolve => {
      this.executeStatement('MATCH (n) RETURN n;').then(
        nodes => {
          this.executeStatement('MATCH p=()-[r]->() RETURN p;').then(
            relationships => {
              const realNodes = nodes.results[0].data.map(node => {
                return node.graph.nodes[0];
              });
              const realLinks = relationships.results[0].data.map(link => {
                return link.graph.relationships[0];
              });

              resolve({nodes: realNodes, links: realLinks});
            }
          )
        }
      )
    });

  }



}



