import {MainProperties} from './mainProperties';
import {TextualProperties} from './textualProperties';
import {GraphProperties} from './graphProperties';
import {MetricProperties} from './metricProperties';
import {MetricGraphicalProp} from './metricGraphicalProp';
import {Description} from './description';
import {SubGroups} from './subGroups';
import {GraphTab} from './graphTab';

/**
 * Created by johannes on 24.07.17.
 */

export class Properties {
  readonly main: MainProperties;
  readonly textual: TextualProperties;
  readonly graph: GraphProperties;
  readonly metrics: MetricProperties;

  public static convertJSONToObject(json: JSON): Properties {
    let mainProp: MainProperties = new MainProperties('');
    let textProp: TextualProperties;
    let metricProp: MetricProperties;
    const graphical: MetricGraphicalProp[] = [];
    const descriptions: Description[] = [];
    const subGroups: SubGroups[] = [];
    let graphProp: GraphProperties;
    const graphTabs: GraphTab[] = [];

    if (json['main']) {
      mainProp = new MainProperties(json['main'].defaultFile,
        json['main'].showAllDropdownElements, json['main'].dropdownElements);
    }
    if (json['textual']) {
      textProp = new TextualProperties(json['textual'].extensions);
    }
    if (json['metrics']) {
      const g = json['metrics'].graphical;
      for (const graphi in g) {
        if (!g.hasOwnProperty(graphi)) {
          continue;
        }
        const charts = g[graphi].graphic;
        let defaultChart = g[graphi].defaultGraphic;
        if (charts && !defaultChart) {
          defaultChart = charts[0];
        }

        const element = new MetricGraphicalProp(graphi,
          charts, defaultChart, g[graphi].showMultipleMetrics,
          g[graphi].relMetric, g[graphi].relMetricX,
          g[graphi].relMetricY, g[graphi].coloring, g[graphi].coloringMin, g[graphi].coloringMax);
        graphical.push(element);
      }

      const d = json['metrics'].descriptions;
      for (const desc in d) {
        if (!d.hasOwnProperty(desc)) {
          continue;
        }
        const element = new Description(desc, d[desc]);
        descriptions.push(element);

      }

      if (json['metrics'].hasOwnProperty('subGroups')) {
        for (const sub of json['metrics'].subGroups) {
          const element = new SubGroups(sub.metrics, sub.types);
          subGroups.push(element);
        }
      }


      metricProp = new MetricProperties(graphical, descriptions, subGroups, json['metrics'].usedOd);
    }
    if (json['graph']) {
      if (json['graph'].hasOwnProperty('tabs')) {
        const t = json['graph'].tabs;
        for (const tab in t) {
          if (!t.hasOwnProperty(tab)) {
            continue;
          }
          const element = new GraphTab(tab, t[tab].rootType, t[tab].linkList, t[tab].flatTypes);
          graphTabs.push(element);
        }
      }
      let  groupItemType = '';
      let subGroupItemType: string[] = [];
      let groupChildLinkTypes: string[] = [];
      let groupCrossLinkTypes: string[] = [];
        if (json['graph'].hasOwnProperty('hierarchy')) {
          groupItemType = json['graph'].hierarchy.groupItemType;
          subGroupItemType = json['graph'].hierarchy.subGroupItemType;
          groupChildLinkTypes = json['graph'].hierarchy.groupChildLinkTypes;
          groupCrossLinkTypes = json['graph'].hierarchy.groupCrossLinkTypes;

        }

        graphProp = new GraphProperties(groupItemType, subGroupItemType,
          groupChildLinkTypes, groupCrossLinkTypes, graphTabs);
    }

    return new Properties(mainProp, textProp, graphProp, metricProp);

  }

  constructor(main: MainProperties = null,
              textual: TextualProperties = null,
              graph: GraphProperties = null,
              metrics: MetricProperties = null) {

    if (main != null) {
      this.main = main;
    } else {
      this.main = new MainProperties('');
    }

    if (textual != null) {
      this.textual = textual;
    } else {
      this.textual = new TextualProperties(null);
    }

    if (graph != null) {
      this.graph = graph;
    } else {
      this.graph = new GraphProperties('', [], [], [], []);
    }

    if (metrics != null) {
      this.metrics = metrics;
    } else {
      this.metrics = new MetricProperties();
    }
  }

}



