export class Table {
  readonly name: string;
  readonly columns: any[];
  readonly rows: any[];
  readonly types: string[];
  readonly metrics: string[];

  constructor(columns: any[], rows: any[], types: string[], metrics: string[], name?: string) {
    this.rows = rows;
    this.columns = columns;
    this.types = types;
    this.metrics = metrics;
    if (name) {
      this.name = name;
    } else {
      this.name = metrics.reduce((prev, akt) => {
        return prev + ', ' + akt.substring(0, 3);
      })
    }

  }


}



