import { Injectable } from '@angular/core';
import {Http, Response} from '@angular/http';
import {Observable} from 'rxjs/Rx';
import {HttpService} from '../helper/httpService';

@Injectable()
export class FileContentService extends HttpService {

  // Overrides
  protected static extractData(res: Response) {
    return res.text() || ' ';
  }

  constructor(private http: Http) {
    super();
  }

  getContent(path: string): Observable<string> {
    return this.http.get(path)
      .map(FileContentService.extractData)
      .catch(FileContentService.handleError);

  }

}



