import {Component, OnInit, Input} from '@angular/core';
import {GraphData} from '../helper/graph-data';
import {Tab} from '../helper/tab';
import {TabTree} from './tab-tree';
import {GraphHelperService} from '../helper/graph-helper.service';
import {SelectedProjectService} from '../../selected-project.service';

@Component({
  selector: 'app-graph-tree',
  templateUrl: './graph-tree.component.html',
  styleUrls: ['./graph-tree.component.css']
})
export class GraphTreeComponent implements OnInit {

  private _graph: GraphData;
  private tabs: Array<Tab>;

  @Input()
  set graph(graph: GraphData) {
    this._graph = graph;

    if (graph != null) {

      this.tabs = [];

      this.setUpTabs();

    }

  }

  get graph(): GraphData {
    return this._graph;
  }

  constructor(private selectedProjectService: SelectedProjectService) {

    this.tabs = [];
  }

  ngOnInit() {
  }

  private setUpTabs(): void {
    this.selectedProjectService.getProperties().subscribe(
      prop => {
        prop.graph.tabs.forEach(tab => {
          const name = tab.name;
          const rootType = tab.rootType;
          const linkList = tab.linkList;
          const flatTypes = tab.flatTypes;

          this.setUpTab(name, rootType, linkList, flatTypes);
        })
      }
    );
  }

  private setUpTab(tabName: string, rootType: string, linkTypes: string[], flatTypes?: string[]): void {
    const graph = this.graph;

    const links = graph.links.filter(l => {
      for (const linkType of linkTypes) {
        if (l.forwardLinks[linkType]) {
          return true;
        }
      }
      return false;
    });

    let elements = graph.elements.filter(e => {
      return e.type === rootType && !links.some(l => {
        return l.target === e;
        });
    });

    // Flat View if there is no Hierarchy present
    if (elements.length === 0 && flatTypes) {
      elements = graph.elements.filter(e => {
        for (const flatType of flatTypes) {
          if (e.type === flatType) {
            return true;
          }
        }
        return false;
      });
    }

    this.createTreeTab(tabName, elements, links);


  }

  private createTreeTab (caption, items, links): void {

    const data = this.createTree(items, links);
    this.tabs.push(new Tab(caption, data));

  }

  private createTree(items, links): TabTree<any> {
    const tree = new TabTree(null);

    for (const item of items) {

      const newTreeElement = tree.addChild(item);
      this.createSubTree(newTreeElement, links);

    }

    return tree;
  }

  private createSubTree(root: TabTree<any>, links): void {
    const childrenLinks = links.filter(l => {
      return GraphHelperService.elementEquals(root.element, l.source);
    });

    for (const link of childrenLinks) {
      const child = root.addChild(link.target);
      this.createSubTree(child, links);
    }


  }

}



