import {Component, OnInit, ViewEncapsulation, Input} from '@angular/core';
import {D3, D3Service} from 'd3-ng2-service';
import {GraphCommunicationService} from '../../graph-communication.service';
import {GraphData} from '../../helper/graph-data';
import {EventCommunicationService} from '../../event-communication.service';

@Component({
  selector: 'app-graph-legend',
  templateUrl: './graph-legend.component.html',
  styleUrls: ['./graph-legend.component.css'],
  encapsulation : ViewEncapsulation.None
})
export class GraphLegendComponent implements OnInit {

  private d3: D3;

  private selectedTypes: string[];

  @Input()
  set graph(graph: GraphData) {
    if (graph != null) {
      this.resetLegend();
      this.setUpLegend();
    }
  }

  @Input()
  set isHierarchical(isHierarchical: boolean) {
    this.switchHierarchyMode(isHierarchical);
  }

  constructor(
    d3Service: D3Service,
    private graphCommunicationService: GraphCommunicationService,
    private eventCommunicationService: EventCommunicationService
  ) {
    this.d3 = d3Service.getD3();
  }

  ngOnInit() {
  }

  private resetLegend() {
    if (document.getElementById('legend')) {
      const legend = document.getElementById('legend');
      while (legend.firstChild) {
        legend.removeChild(legend.firstChild);
      }
    }
  }

  private setUpLegend() {
    const d3 = this.d3;
    const graphCommunicationService = this.graphCommunicationService;

    this.selectedTypes = Object.keys(graphCommunicationService.nodeTypes);
    this.eventCommunicationService.selectTypes(this.selectedTypes);

    const legend = d3.select('#legend').selectAll('label')
      .data(Object.keys(graphCommunicationService.nodeTypes))
      .enter()
      .append('label')
      .style('background', d => {

        return graphCommunicationService.nodeTypes[d];
      });
    legend.append('input')
      .attrs({'type': 'checkbox', 'checked': true})
      .on('change', e => {
        this.typeChecked(e);
      });

    legend.append('text').text(d => {
      return d;
    });
  }

  private switchHierarchyMode(on: boolean) {
    if (on) {
      this.d3.selectAll('#legend input')
        .attr('disabled', 'disabled');
      this.d3.selectAll('#legend input')
        .property('checked', true);
    } else {
      this.d3.selectAll('#legend input')
        .attr('disabled', null);
    }
  }

  private typeChecked(type: string) {
    this.selectedTypes = <string[]> this.d3.select('#legend').selectAll('input:checked').data();
    this.eventCommunicationService.selectTypes(this.selectedTypes);
  }

}



