import {Component, OnInit, ViewEncapsulation, Input, Output, EventEmitter, AfterViewInit} from '@angular/core';
import {MetricService} from '../metric.service';
import {Metric} from '../helper/metric';
import {SelectedProjectService} from '../../selected-project.service';
import {Table} from '../helper/table';
import {NgbTabChangeEvent} from '@ng-bootstrap/ng-bootstrap';
import {Description} from '../../helper/description';
import {SubGroups} from '../../helper/subGroups';

@Component({
  selector: 'app-metric-table',
  templateUrl: './metric-table.component.html',
  styleUrls: ['./metric-table.component.css'],
  encapsulation: ViewEncapsulation.None,
})
export class MetricTableComponent implements OnInit, AfterViewInit {

  private allMetrics: Metric[];
  private subTables: Table[];

  @Input() private selectedMetric: string;
  @Input() private coloring: any;
  @Output() public onSubTableChange = new EventEmitter<Table>();

  private standardColumns = [
    {
      name : 'Artifacts',
      prop : 'artifact',
      defaultWidth : 200
    },
    {name : 'Type', prop : 'type', defaultWidth: 150}
  ];

  constructor(
    private metricService: MetricService,
    private selectedProjectService: SelectedProjectService
  ) {
    metricService.metrics$.subscribe(
      metrics => {
        if (metrics.length > 0) {
          this.allMetrics = metrics;
          this.update();
        }
      }
    )
  }

  ngOnInit() {
  }

  ngAfterViewInit() {
    const metrics = this.metricService.getMetrics().getValue();
    if (metrics.length > 0) {
      this.allMetrics = metrics;
      this.update();
    }
  }

  private update(): void {
    // Get Properties also updates SubTables
    this.getProperties();
  }

  private getProperties(): Promise<any> {
    return new Promise(resolve => {
      this.selectedProjectService.getProperties().subscribe(
        properties => {
          const subGroups = properties.metrics.subGroups;
          const descriptions = properties.metrics.descriptions;

          this.makeSubTables(subGroups, descriptions);

          resolve();
        }
      )
    });
  }

  private makeSubTables(subGroups: SubGroups[], descriptions: Description[]) {
    // If no subGroups show all Metrics
    if (subGroups.length === 0) {
      const metrics: string[] = [];
      const types: string[] = [];

      this.allMetrics.forEach(metric => {
        if (metrics.indexOf(metric.name) === -1) {
          metrics.push(metric.name);
        }
        metric.data.forEach(item => {
          if (types.indexOf(item.type) === -1) {
            types.push(item.type);
          }
        });
      });

     subGroups = [new SubGroups(metrics, types)];
    }


    this.subTables = [];
    for (const subGroup of subGroups) {
      const metrics: string[] = subGroup['metrics'];
      const types: string[] = subGroup['types'];

      // Slice(0) is Cloning
      const columns: any[] = this.standardColumns.slice(0);
      const rows: any[] = [];

      for (const metric of metrics) {
        const name = metric.toLocaleLowerCase().replace(' ', '');

        const description: Description = descriptions.find(d => {
          return d.name === metric;
        });

        const column = {name : metric, prop : name, defaultWidth: 100};
        if (description) {
          column['description'] = description.description;
        }
        columns.push(column);

        const metricData = this.allMetrics.find(m => {
          return metric === m.name;
        })
          .data;

        for (const metricDataItem of metricData) {
          let row = rows.find(r => {
            return r['artifact'] === metricDataItem.name;
          });

          // No triple equals
          if (row == null) {
            row = {artifact : metricDataItem.name, type : metricDataItem.type};

            for (const innerMetric of metrics) {
              const innerName = innerMetric.toLocaleLowerCase().replace(' ', '');
              row[innerName] = '-';
            }

            rows.push(row);
          }

          let value = metricDataItem.value;
          if (typeof value === 'number') {
            value = Math.round(value * 10000) / 10000;
          }

          row[name] = value;
        }

      }
      const newTable = new Table(columns, rows, types, metrics);
      this.subTables.push(newTable);

    }
    this.onSubTableChange.emit(this.subTables[0]);

  }

  private subTableChanged ($event: NgbTabChangeEvent) {
    const nextTableName = $event.nextId.substr(4);
    const nextTable = this.subTables.find(e => {
      return nextTableName === e.name;
    });
    this.onSubTableChange.emit(nextTable);
  }


  private mustBeColored(column) {
    return this.coloring.active && column.name === this.selectedMetric;
  }

  private getColor = function(value) {

    const max = this.coloring.max;
    const min = this.coloring.min;

    const normalizedValue = value - min / (max - min);

    /**
     * between 1 and 2
     * if set to 1 then 0.5 value is more olive than yellow
     * if set to 2 then differences between values are harder to see
     */
    const yellowModifier = 1.7;

    let red = Math.round(yellowModifier *  255 * normalizedValue);
    let green = Math.round(yellowModifier * 255 * (1 - normalizedValue));
    const blue = 0;

    // Max values
    if (red > 255) {
      red = 255;
    }
    if (green > 255) {
      green = 255;
    }

    return 'rgb(' + red + ',' + green + ',' + blue + ')';
  }

}



