import { Injectable } from '@angular/core';
import {SelectedProjectService} from '../selected-project.service';
import {HttpService} from '../helper/httpService';
import {Http} from '@angular/http';
import {Observable, BehaviorSubject} from 'rxjs/Rx';
import {Metric} from './helper/metric';
import {MetricDataItem} from './helper/metric-data-item';

@Injectable()
export class MetricService extends HttpService {

  private projectPath: string;

  private _metrics = new BehaviorSubject<Metric[]>([]);

  public metrics$ = this._metrics.asObservable();

  constructor(
    private selectedProjectService: SelectedProjectService,
    private http: Http
  ) {
    super();

    selectedProjectService.selectedProject$.subscribe(
      newProject => {
        if (newProject.name !== '') {
          this.projectPath = newProject.path;
          this.readData().subscribe(
            data => this.convert(data)
          )
        }
      }
    );

  }

  public getMetrics(): BehaviorSubject<Metric[]> {
    return this._metrics;
  }

  private readData(): Observable<JSON[]> {
    const tablePath = this.projectPath + 'metrics/metrics.json';

    return this.http.get(tablePath)
      .map(SelectedProjectService.extractData)
      .catch(SelectedProjectService.handleError)
  }

  private convert(data: JSON[]): void {
    const metrics: Metric[] = [];

    for (const metric of data) {
      const metricName = metric['name'];
      const metricData: MetricDataItem[] = [];

      for (const dataItem of metric['data']) {
        const dataItemName: string = dataItem['artifact'];
        const dataItemType: string = dataItem['type'];
        const dataItemValue: number = dataItem['value'];

        metricData.push(new MetricDataItem(dataItemName, dataItemType, dataItemValue));
      }

      metrics.push(new Metric(metricName, metricData));

    }

    this._metrics.next(metrics);
  }


}



