import { Component, OnInit } from '@angular/core';
import {SelectedProjectService} from '../../selected-project.service';

@Component({
  selector: 'app-project-date',
  templateUrl: './project-date.component.html',
  styleUrls: ['./project-date.component.css'],
})
export class ProjectDateComponent implements OnInit {
  date: Date;


  constructor(private selectedProjectService: SelectedProjectService) {
    selectedProjectService.projectDate$.subscribe(
       date => {
         this.date = date;
       }
    )

  }

  ngOnInit() {
  }
}



