import {Injectable} from '@angular/core';

import {Project} from './helper/project';
import {Objectdiagram} from './helper/objectdiagram';
import {AllProjectsService} from './all-projects.service';
import {Http} from '@angular/http';
import {BehaviorSubject, Observable} from 'rxjs/Rx';
import {HttpService} from './helper/httpService';
import {Properties} from './helper/properties';
import {MainProperties} from './helper/mainProperties';
import {TextualProperties} from './helper/textualProperties';
import {GlobalProperties} from './helper/globalProperties';

@Injectable()
export class SelectedProjectService extends HttpService {

  private _selectedProject = new BehaviorSubject<Project>(new Project('', ''));
  private _odList = new BehaviorSubject <Array<Objectdiagram>>([]);
  private _folderStructure = new BehaviorSubject<Array<JSON>>(JSON.parse('[]'));
  private _projectDate = new BehaviorSubject<Date>(new Date());
  private _properties = new BehaviorSubject<Properties>(new Properties());
  private propertiesUpToDate: boolean;

  selectedProject$ = this._selectedProject.asObservable();
  odList$ = this._odList.asObservable();
  folderStructure$ = this._folderStructure.asObservable();
  projectDate$ = this._projectDate.asObservable();
  properties$ = this._properties.asObservable();

  globalProperties: GlobalProperties;

  constructor(private allProjectsService: AllProjectsService,
              private http: Http) {
    super();
    this.propertiesUpToDate = true;

    this.setStateToDefault();
  }

  private onGlobalPropertiesRead(globalProperties: GlobalProperties) {
    this.globalProperties = globalProperties;

    const defaultProjectName = this.globalProperties.defaultProject;
    const defaultProjectPath = 'assets/' + defaultProjectName + '/';

    const defaultProject = new Project(defaultProjectName, defaultProjectPath);
    this.setSelectedProject(defaultProject);

  }

  public setStateToDefault() {
    this.allProjectsService.getGlobalProperties()
      .subscribe(
        properties => this.onGlobalPropertiesRead(properties),
        error => console.log(error)
      );
  }

  private parseOdList(json: Array<JSON>): Array<Objectdiagram> {
    const result = [];

    for (const jsonObject of json) {
      const pathToCut = this.getSelectedProject().getValue().path + 'textFiles/';

      const name = jsonObject['name'].substr(pathToCut.length);
      const path = jsonObject['name'];
      const jsonPath = jsonObject['jsonPath'];

      const od = new Objectdiagram(name, path, jsonPath);
      result.push(od);


    }

    result.sort((a, b) => {
      return a.name.localeCompare(b.name);
    });

    return result;

  }

  public getDisplayableOdList(): Promise<Array<Objectdiagram>> {
    let result = this.getOdList().getValue();
    return new Promise(resolve => {
      this.getProperties().subscribe(
        prop => {
          if (prop.main.showAllDropdownElements) {
            resolve(result);
          } else {
            result = result.filter(od => {
              return prop.main.dropdownElements.indexOf(od.name) > -1;
            });
            resolve(result);
          }
        }
      )
    });
  }

  public getSelectedProject(): BehaviorSubject<Project> {
    return this._selectedProject;
  }

  public getOdList(): BehaviorSubject<Array<Objectdiagram>> {
    return this._odList;
  }

  public setSelectedProject(project: Project) {
    this.propertiesUpToDate = false;
    this._selectedProject.next(project);

    // Load Date
    this.getDate().then(
      date => this._projectDate.next(date),
      error => console.log(error)
    );

    // Load OD List
    const dataPath = project.path + 'textFiles';
    this.http.post('getAllOd.php', dataPath)
      .map(SelectedProjectService.extractData)
      .catch(SelectedProjectService.handleError)
      .subscribe(
        allOds => {
          this.setOds(this.parseOdList(allOds));

          // Load Properties
          this.getProperties().subscribe(
            properties => {
              this._properties.next(properties);
              this.propertiesUpToDate = true;

              // Load Folder Structure
              this.makeFolderStructure().then(
                folderStructure => {
                  this.setFolderStructure(folderStructure);
                }
              );
            },
            error => console.log(error)
          );
        },
        error => console.log(error)
      );


  }


  private loadProperties(): Observable<Properties> {
    const propertiesPath = this.getSelectedProject().getValue().path + 'properties.json';

    return this.http.get(propertiesPath)
      .map(res => {
        const json: JSON = SelectedProjectService.extractData(res);
        return Properties.convertJSONToObject(json);
      })
      .catch(SelectedProjectService.handleErrorWith404Acceptance)
  }

  public getProperties(): Observable<Properties> {
    if (this.propertiesUpToDate) {
      const properties = this._properties.getValue();
      return Observable.of(properties);
    } else {
      return new Observable(observer => {
        this.loadProperties().subscribe(
          prop => {
            this.resolveProperties(prop).then(
              resProp => {
                observer.next(resProp);
              }
            )
          }
        )
      });
    }
  }

  private resolveProperties(prop: Properties): Promise<Properties> {
    return new Promise(resolve => {
      if (prop == null) {
        this.getFirstOdPath().then(
          path => {
            const mainProp = new MainProperties(path);
            resolve(new Properties(mainProp));
          }
        );
      } else {
        if (!(prop.main.defaultFile && prop.main.defaultFile !== '')) {
          this.getFirstOdPath().then(
            path => {
              const mainProp = new MainProperties(path, prop.main.showAllDropdownElements, prop.main.dropdownElements);
              resolve(new Properties(mainProp, prop.textual, prop.graph, prop.metrics))
            }
          );
        } else {
          resolve(prop);
        }
      }
    });
  }

  private getFirstOdPath(): Promise<string> {
    return new Promise(resolve => {
      this.getOdList().subscribe(
        list => {
          if (list.length > 0) {
            const path = list[0].path.substring(this.getSelectedProject().getValue().path.length);
            resolve(path);
          } else {
            resolve('')
          }
        }
      )
    })
  }

  private setOds(ods: Array<Objectdiagram>) {
    this._odList.next(ods);
  }

  private setFolderStructure(folderStructure: JSON[]) {
    this._folderStructure.next(folderStructure);
  }

  private getDate(): Promise<Date> {
    return new Promise((resolve, reject) => {
      const projectPath = this.getSelectedProject().getValue().path;
      if (projectPath === '') {
        reject('No Project');
      }
      const path = projectPath + 'textFiles/';
      this.http.post('getFileDate.php', path)
        .map(SelectedProjectService.extractData)
        .catch(SelectedProjectService.handleError)
        .subscribe(
          date => {
            const result = new Date(date * 1000);
            resolve(result);
          }
        );
    });
  }


  private makeFolderStructure(): Promise<Array<JSON>> {
    return new Promise((resolve, reject) => {
      const projectRoot = this.getSelectedProject().getValue().path;
      this.getProperties().subscribe(
        prop => {
          this.http.post('convertFolder.php', projectRoot)
            .map(SelectedProjectService.extractData)
            .catch(SelectedProjectService.handleError)
            .subscribe(
              folderStructure => {
                resolve(this.translateFolderStructure(folderStructure, prop.textual));
              },
              error => {
                console.log(error);
                reject(error);
              }
            );
        }
      );
    });

  }

  private translateFolderStructure(json: Array<JSON>, prop: TextualProperties): Array<JSON> {
    let result = json;
    const fileExtensions = prop.extensions;
    if (fileExtensions != null) {
      result = json.filter(e => {
        if (e['ext'] === 'folder') {
          e['children'] = this.translateFolderStructure(e['children'], prop);
          return e['children'].length > 0;
        }
        return fileExtensions.indexOf(e['ext']) !== -1;
      });
    }
    return result;
  }


  public getOdFromPath(path: string): Objectdiagram {
    const odList = this.getOdList().getValue();
    for (const od of odList) {
      if (od.path.endsWith(path)) {
        return od;
      }
    }

    return null;
  }


}



