import { Injectable } from '@angular/core';

@Injectable()
export class GraphCommunicationService {

  private _selectedNodes;
  private _nodeTypes;

  constructor() {
    this._selectedNodes = [];
  }

  set selectedNodes(selected) {
    this._selectedNodes = selected;
  }

  get selectedNodes() {
    return this._selectedNodes;
  }

  set nodeTypes(nodeTypes) {
    this._nodeTypes = nodeTypes;
  }

  get nodeTypes() {
    return this._nodeTypes;
  }



}



