import {Component, OnInit, Input, ViewEncapsulation, ViewChild} from '@angular/core';
import {GraphData} from '../helper/graph-data';
import {Tab} from '../helper/tab';
import {NgbTabChangeEvent, NgbTabset} from '@ng-bootstrap/ng-bootstrap';
import {EventCommunicationService} from '../event-communication.service';
import {SelectedProjectService} from '../../selected-project.service';

@Component({
  selector: 'app-graph-main',
  templateUrl: './graph-main.component.html',
  styleUrls: ['./graph-main.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class GraphMainComponent implements OnInit {

  @ViewChild(NgbTabset)
  private hierarchyTabs: NgbTabset;

  private _graph: GraphData;
  private tabs: Array<Tab>;

  private showHierarchicalGraph;

  private layoutActive: boolean;
  private nodeCollision: boolean;
  private allLinks: boolean;
  private clickToAdd: boolean;

  private nodeCollisionActive: boolean;

  @Input()
  set graph(graph: GraphData) {
    this._graph = graph;

    if (graph != null) {

      this.tabs = [];
      this.setUpTabs();

    }

  }

  get graph(): GraphData {
    return this._graph;
  }

  constructor(
    private eventCommunicationService: EventCommunicationService,
    private selectedProjectService: SelectedProjectService
  ) {
    this.tabs = [];
    this.layoutActive = true;
    this.nodeCollision = true;
    this.allLinks = false;
    this.clickToAdd = false;

    this.nodeCollisionActive = true;

    this.showHierarchicalGraph = false;

    eventCommunicationService.itemSelected$.subscribe(
      () => this.hierarchyTabs.select('tab-Flat')
    );
  }

  ngOnInit() {
  }

  private setUpTabs() {
    this.setUpFlatTab();
    this.setUpHierarchyTab();

    this.hierarchyTabs.select('tab-Flat');
    this.showHierarchicalGraph = false;
  }

  private setUpFlatTab() {
    const caption = 'Flat';
    const data: GraphData = this.graph;
    this.tabs.push(new Tab(caption, data));
  }

  private setUpHierarchyTab() {
    const caption = 'Hierarchy';
    const data = this.graph;

    this.getGroupItemType().then(
      groupItemType => {
        const topElements = data.elements.some(e => {
          return e.type === groupItemType;
        });
        this.tabs.push(new Tab(caption, data, topElements, true));
      }
    );


  }

  private resetZoom() {
    this.eventCommunicationService.resetZoomClicked();
  }

  private collapseAll() {
    this.eventCommunicationService.collapseAllClicked();
  }

  private expandAll() {
    this.eventCommunicationService.expandAllClicked();
  }

  private hierarchyTabChanged(event: NgbTabChangeEvent) {
    switch (event.nextId) {
      case 'tab-Flat': this.showHierarchicalGraph = false;
      break;
      case 'tab-Hierarchy': this.showHierarchicalGraph = true;
      break;
      default: console.log('Tab Error');
    }
  }

  private layoutActiveTriggered() {
    this.nodeCollision = this.layoutActive;
    this.nodeCollisionActive = this.layoutActive;
  }

  private getGroupItemType(): Promise<string> {

    return new Promise((resolve, reject) => {
      this.selectedProjectService.getProperties()
        .subscribe(
          properties => {
            // if(properties.graph.groupItemType != '') {
              resolve(properties['graph'].groupItemType);
            // }
            // resolve('');
          },
          error => {
            reject(error);
          }
        );
    });
  }



}



