import { Injectable } from '@angular/core';
import {GraphTreeService} from './graph-tree.service';
import {GraphHelperService} from './graph-helper.service';
import {GraphData} from './graph-data';
import {GraphCommunicationService} from '../graph-communication.service';

@Injectable()
export class GraphCanvasHelperService {

  public static closedNeighbourhood(graph, nodes, allLinks) {

    const result = { elements: [], links: [], groups: [] };

    nodes.forEach(function(n) {

      result.elements.push(n);
    });

    graph.links.forEach(function(l) {

      if (nodes.indexOf(l.source) > -1) {

        if (result.elements.indexOf(l.target) < 0) {

          result.elements.push(l.target);
        }
        if (!allLinks) {

          result.links.push(l);
        }
      } else if (nodes.indexOf(l.target) > -1) {

        if (result.elements.indexOf(l.source) < 0) {

          result.elements.push(l.source);
        }
        if (!allLinks) {

          result.links.push(l);
        }
      }
    });
    if (allLinks) {

      result.links = graph.links.filter(function(l) {

        return result.elements.indexOf(l.source) > -1 && result.elements.indexOf(l.target) > -1;
      });
    }
    return result;
  }

  public static getNodeId(node) {
    const id = node.isGroup ? 'g' + node.id : node.id;
    return node.inHierarchyGraph ? id + 'h' : id;
  }

  public static createContextMenu(contextMenu) {

    contextMenu.selectAll('*').remove();

    return contextMenu;
  };

  public static showContextMenu(contextMenu, e, coordinates: number[]) {

    e.preventDefault();

    // keep cm in window bounds
    const bBox = contextMenu.node().getBoundingClientRect();

    const x = Math.min(coordinates[0], window.innerWidth - bBox.width);
    const y = Math.min(coordinates[1], window.innerHeight - bBox.height);

    contextMenu.style('left', x + 'px').style('top', y + 'px');
  };

  public static appendProperties(li, properties) {

    li.append('ul').selectAll('li').data(Object.keys(properties)).enter()
      .append('li').html(function(key) {

      if (typeof properties[key] !== 'number') {
        return '  ' + key + ' = "' + properties[key] + '"';
      } else {
        return '  ' + key + ' = ' + properties[key];
      }

    });
  };

  public static reOrderGraph(graph) {
    GraphCanvasHelperService.restoreLinks(graph);

    const newGrouping = [];
    let toInsert = graph.groups.filter(g => {
      return g.myParent === null
    });

    while (toInsert.length > 0) {
      const g = toInsert.pop();
      if (g.groups) {
        toInsert = toInsert.concat(g.groups);
      }

      let parentIndex;
      if (g.myParent === null) {
        parentIndex = -1;
      } else {
        parentIndex = newGrouping.indexOf(g.myParent);
      }
      newGrouping.splice(parentIndex + 1, 0, g);

    }
    graph.groups = newGrouping;

    // Regroup Elements
    graph.elements.sort((a, b) => {
      const sameGroup = graph.groups.indexOf(a.myParent) - graph.groups.indexOf(b.myParent);
      if (!sameGroup) {
        return sameGroup
      } else {
        return a.id - b.id;
      }
    });

    GraphCanvasHelperService.reIndexGroups(graph);
  }

  public static restoreLinks(graph) {
    // Restore Links instead of numbers
    graph.groups.forEach(g => {
      if (typeof g.groups !== 'undefined') {
        g.groups.forEach((l, i) => {
          if (typeof l === 'number') {
            g.groups[i] = graph.groups[l];
          }
        })
      }

      g.leaves.forEach(function(l, i) {
        if (typeof l === 'number') {
          g.leaves[i] = graph.elements[l];
        }
      });

    });
  }

  public static reIndexGroups(graph) {

    graph.groups.forEach(function(g) {

      if (typeof g.groups !== 'undefined') {
        g.groups.forEach(function (l, i) {
          if (typeof l !== 'number') {
            g.groups[i] = graph.groups.indexOf(l);
          }
        });
      }

      g.leaves.forEach(function(l, i) {
        if (typeof l !== 'number') {
          g.leaves[i] = graph.elements.indexOf(l);
        }
      });
    });

  }

  private static createDerivedLink(l, newSource, newTarget) {
    const hybrid = {
      source: newSource,
      target: newTarget,
      originalSourceTypes: [l.source.type],
      originalTargetTypes: [l.target.type],
      forwardLinks: {},
      backwardLinks: {}
    };

    for (const fKey in l.forwardLinks) {
      if (l.forwardLinks.hasOwnProperty(fKey)) {
        hybrid.forwardLinks['*' + fKey] = l.forwardLinks[fKey];
      }
    }
    for (const bKey in l.backwardLinks) {
      if (l.backwardLinks.hasOwnProperty(bKey)) {
        hybrid.backwardLinks['*' + bKey] = l.backwardLinks[bKey];
      }
    }
    return hybrid;

  }

  constructor(
    private graphTreeService: GraphTreeService,
    private graphCommunicationService: GraphCommunicationService
  ) { }

  public createGroupGraphInitial (type, childLinkTypes, crossLinkTypes, graph): GraphData {

    this.graphTreeService.reset();

    const result = new GraphData();

    this.graphTreeService.setTopType(type);

    // Check if Element has Children?
    graph.elements.forEach(e => {
      const parent = this.findParent(e, childLinkTypes, graph);

      const element = {
        id: e.id,
        name: e.name,
        type: e.type,
        attributes: e.attributes,
        inHierarchyGraph: true
      };
      this.graphTreeService.push(element, parent);

    });

    this.graphTreeService.createTree();

    const treeElements = this.graphTreeService.getTree().children.filter(function (e) {
      return e.element.type === type;
    });
    treeElements.forEach(function (e) {
      e.element.myParent = null;
      e.element.isGroup = (e.children.length > 0);
    });

    result.elements = treeElements.map(function (e) {
      return e.element;
    });

    result.links = this.createGraphLinks(result.elements, crossLinkTypes, graph);
    this.mapLinks(result);

    return result;

  }

  /*
   Calculates the links between elements (and all of their children)
   @elements: all elements whose links are calculated
   @crossLinkTypes Linktypes which are included
   @graph: the underlying graph containing all elements, subelements and links
   */
  public createGraphLinks(elements, crossLinkTypes, graph) {

    const topLinks = [];
    const derivedLinks = [];
    const hybridLinks = [];

    const linkSet = graph.links.filter(function(l) {
      if (typeof crossLinkTypes !== 'undefined' && crossLinkTypes != null && crossLinkTypes.length > 0) {
        for (const linkType of crossLinkTypes) {
          if (l.forwardLinks[linkType] || l.backwardLinks[linkType]) {
            return true;
          }
        }
      }
      return false;
    }).map(function (l) {
      return {
        backwardLinks: l.backwardLinks,
        forwardLinks: l.forwardLinks,
        source: l.source,
        target: l.target
      };

    });

    linkSet.forEach(l => {
      let source = elements.find((e) => {
        return GraphHelperService.elementEquals(e, l.source);
      });
      let target = elements.find((e) => {
        return GraphHelperService.elementEquals(e, l.target);
      });

      if ((typeof source !== 'undefined') && (typeof target !== 'undefined')) {
        topLinks.push(l);
      } else {

        if (typeof source === 'undefined') {
          const sources = this.graphTreeService.getAncestors(l.source);

          if (typeof sources === 'undefined') {
            return;
          }

          source = elements.find((e) => {
            for (let i = 0; i < sources.length; i++) {
              if (GraphHelperService.elementEquals(e, sources[i])) {
                return true;
              }
            }
            return false;
          });
        }

        if (typeof target === 'undefined') {
          const targets = this.graphTreeService.getAncestors(l.target);

          if (typeof targets === 'undefined') {
            return;
          }

          target = elements.find((e) => {
            for (let i = 0; i < targets.length; i++) {
              if (GraphHelperService.elementEquals(e, targets[i])) {
                return true;
              }
            }
            return false;
          });
        }

        if (!GraphHelperService.isEmpty(source)  && !GraphHelperService.isEmpty(target)) {
          hybridLinks.push(l);

          if (source !== target) {
            const derivedLink = GraphCanvasHelperService.createDerivedLink(l, source, target);
            derivedLinks.push(derivedLink);
          }
        }
      }
    });

    const allLinks = topLinks.concat(derivedLinks);
    return this.mergeLinks(allLinks);
  }

  /*
   Maps the source und target objects of the links to the right copies
   */
  public mapLinks(graph) {
    graph.links.forEach(l => {

      graph.elements.find(e => {
        return GraphHelperService.elementEquals(l.source, e);
      });

      graph.elements.find(e => {
        return GraphHelperService.elementEquals(l.target, e);
      });

    });

  }

  /*
   return parent element of node, if it has none, returns null
   */
  private findParent(node, childLinkTypes, graph) {

    const temp = graph.links.find(function(l) {
      if (l.target !== node) {
        return false;
      }

      if (typeof childLinkTypes !== 'undefined' && childLinkTypes != null && childLinkTypes.length > 0) {
        for (const linkType of childLinkTypes) {
          if (l.forwardLinks[linkType]) {
            return true;
          }
        }
      }
      return false;

    });

    if (typeof temp !== 'undefined') {
      return temp.source;
    } else {
      return null;
    }

  }

  private mergeLinks(links) {
    const result = [];

    links.forEach(l => {
      this.addToGroupLinks(result, l);
    });

    return result;

  }

  private addToGroupLinks(groupLinks, link) {

    let groupLink = groupLinks.find(l => {

      return GraphHelperService.elementEquals(l.source, link.source) && GraphHelperService.elementEquals(l.target, link.target);

    });

    if (groupLink) {

      this.addToGroupLink(groupLink, link, true);

    } else {

      groupLink = groupLinks.find(l => {

        return GraphHelperService.elementEquals(l.source, link.target) && GraphHelperService.elementEquals(l.target, link.source);

      });
      if (groupLink) {

        this.addToGroupLink(groupLink, link, false);

      } else {

        groupLinks.push(link);
      }
    }
  }

  private addToGroupLink(groupLink, link, sameDirection) {

    const links = [link.forwardLinks, link.backwardLinks];
    let groupLinks;

    if (sameDirection) {

      groupLinks = [groupLink.forwardLinks, groupLink.backwardLinks];

    } else {

      groupLinks = [groupLink.backwardLinks, groupLink.forwardLinks];
    }
    for (let i = 0; i < 2; ++i) {

      for (const key in links[i]) {
        if (links[i].hasOwnProperty(key)) {
          if (groupLinks[i][key]) {
            groupLinks[i][key] += links[i][key];
          } else {
            groupLinks[i][key] = links[i][key];
          }
        }
      }
    }

    if (typeof groupLink.originalSourceTypes === 'undefined') {
      groupLink.originalSourceTypes = [groupLink.source.type];
      groupLink.originalTargetTypes = [groupLink.target.type];
    }
    if (typeof link.originalSourceTypes === 'undefined') {
      link.originalSourceTypes = [link.source.type];
      link.originalTargetTypes = [link.target.type];
    }

    groupLink.originalSourceTypes =
      this.mergeArraysWithoutDuplicates(groupLink.originalSourceTypes, link.originalSourceTypes);

    groupLink.originalTargetTypes =
      this.mergeArraysWithoutDuplicates(groupLink.originalTargetTypes, link.originalTargetTypes);

  }

  private mergeArraysWithoutDuplicates(source, target) {
    const result = source;

    target.forEach(function (targetElement) {
      let contained = false;
      source.forEach(function (sourceElement) {
        if (targetElement === sourceElement) {
          contained = true;
        }
      });

      if (!contained) {
        result.push(targetElement);
      }

    });

    return result;
  }

  public appendLinkDescription(list, links, link) {

    const li = list.append('li');

    if (link.originalSourceTypes) {
      link.originalSourceTypes.forEach( type => {
        li.append('span').style('background',
          this.graphCommunicationService.nodeTypes[type]).text(type);
      });
    } else {
      const source = link.source;
      li.append('span').style('background',
        this.graphCommunicationService.nodeTypes[source.type]).text(GraphHelperService.getNodeName(source));
    }

    li.append('span').text('');


    if (link.originalTargetTypes) {
      link.originalTargetTypes.forEach( type =>  {
        li.append('span').style('background',
          this.graphCommunicationService.nodeTypes[type]).text(type);
      });
    } else {
      const target = link.target;
      li.append('span').style('background',
        this.graphCommunicationService.nodeTypes[target.type]).text(GraphHelperService.getNodeName(target));
    }

    GraphCanvasHelperService.appendProperties(li, links);
  };


}



