export class Tab {

  public name: string;
  public content: any;
  public active: boolean;
  public hierarchy: boolean;

  constructor(name: string, content: any, active= true, hierarchy: boolean= false) {
    this.name = name;
    this.content = content;
    this.active = active;
    this.hierarchy = hierarchy;
  }

}



