import { Injectable } from '@angular/core';

import { Project } from './helper/project';
import {Observable} from 'rxjs/Rx';
import {Http, Response} from '@angular/http';

import {HttpService} from './helper/httpService';
import {GlobalProperties} from './helper/globalProperties';

@Injectable()
export class AllProjectsService extends HttpService {

  private globalPropertyUrl = 'assets/globalProperties.json';

  private static extractAllProjectData(res: Response) {
    const json = AllProjectsService.extractData(res);

    const result = [];

    for (const jsonObject of json) {
      const od = new Project(jsonObject['name'], jsonObject['rootPath']);
      result.push(od);
    }

    return result;

  }

  constructor(private http: Http) {
    super();
  }

  public getProjects(): Observable<Project[]> {
    return this.http.post('getProjects.php', 'assets/')
      .map(AllProjectsService.extractAllProjectData)
      .catch(AllProjectsService.handleError);
  }

  public getGlobalProperties(): Observable<GlobalProperties> {
    return new Observable(observer => {
      this.loadGlobalProperties().subscribe(
        prop => {
          this.resolveGlobalProperties(prop).then(
            resProp => {
              observer.next(resProp);
              observer.complete();
            }
          )
        }
      )
    });
  }

  private loadGlobalProperties(): Observable<GlobalProperties> {

    return this.http.get(this.globalPropertyUrl)
      .map(res => {
        const json: JSON = AllProjectsService.extractData(res);
        return GlobalProperties.convertJSONToObject(json);
      })
      .catch(AllProjectsService.handleErrorWith404Acceptance);
  }

  private resolveGlobalProperties(prop: GlobalProperties): Promise<GlobalProperties> {
    return new Promise(resolve => {
      if (prop == null || prop.defaultProject === '') {
        this.getProjects().subscribe(
          list => {
            if (list.length > 0) {
              resolve(new GlobalProperties(list[0].name, 'neo4j'));
            }
          }
        );
      } else {
        resolve(prop);
      }
    });
  }


}



