import {NgModule, ModuleWithProviders} from '@angular/core';
import { CommonModule } from '@angular/common';
import { MetricComponent } from './metric/metric.component';

import { MetricRoutingModule } from './metric-routing.module';
import { MetricTableComponent } from './metric-table/metric-table.component';
import { MetricChartsComponent } from './metric-charts/metric-charts.component';
import {MetricService} from './metric.service';
import {SelectedProjectService} from '../selected-project.service';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {FormsModule} from '@angular/forms';
import {SelectedOdService} from '../selected-od.service';
import {NgxChartsModule} from '@swimlane/ngx-charts';
import {D3Service} from 'd3-ng2-service';
import {NgxDatatableModule} from '@swimlane/ngx-datatable';

@NgModule({
  imports: [
    CommonModule,
    MetricRoutingModule,
    NgbModule,
    FormsModule,
    NgxChartsModule,
    NgxDatatableModule
  ],
  declarations: [MetricComponent, MetricTableComponent, MetricChartsComponent],
  providers: [MetricService]
})
export class MetricModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: MetricModule,
      providers: [SelectedProjectService, SelectedOdService, D3Service]
    };
  }
}



