import {Component, OnInit, AfterViewInit} from '@angular/core';
import {SelectedOdService} from '../../selected-od.service';
import {Objectdiagram} from '../../helper/objectdiagram';
import {GraphHelperService} from '../helper/graph-helper.service';
import {GraphData} from '../helper/graph-data';
import {D3Service, D3} from 'd3-ng2-service';
import {GraphCommunicationService} from '../graph-communication.service';
import {EventCommunicationService} from '../event-communication.service';
import {ColorService} from '../../color.service';

@Component({
  selector: 'app-graph',
  templateUrl: './graph.component.html',
  styleUrls: ['./graph.component.css']
})
export class GraphComponent implements OnInit, AfterViewInit {

  private d3: D3;
  private graph: GraphData;

  constructor(
    private d3Service: D3Service,
    private selectedOdService: SelectedOdService,
    private graphCommunicationService: GraphCommunicationService,
    private eventCommunicationService: EventCommunicationService,
    private colorService: ColorService
  ) {

    this.d3 = d3Service.getD3();

    selectedOdService.selectedOd$.subscribe(
      () => this.update()
    );

    colorService.domain$.subscribe(
      (value) => {
        this.updateColors();
      }
    );

  }

  ngOnInit() {
    const treeView = document.getElementById('tree');
    const splitter = document.getElementById('splitter');
    let dragging = null;

    splitter.addEventListener('mousedown', function(e) {

      e.preventDefault();

      dragging = e.offsetX;
    });
    document.body.addEventListener('mousemove', e => {

      if (dragging != null) {

        const w = e.x - dragging;
        this.d3.select(treeView).style('flex', '0 0 ' + w + 'px');

        this.eventCommunicationService.splitterMoved();
      }
    });
    document.body.addEventListener('mouseup', function() {
      dragging = null;
    });
  }

  ngAfterViewInit() {
    setTimeout(() => this.update(), 0);
  }

  private update() {
    this.selectedOdService.getJsonData().subscribe(
      graph => {
        this.graph = this.graphDataChanged(graph);
      },
      error => console.log(error)
    );
  }

  private graphDataChanged(graph: JSON): GraphData {
    let colorsUsed = 0;
    const d3 = this.d3;
    // let color: ScaleOrdinal<number, string> = d3.scaleOrdinal<number, string>(d3.schemeCategory20);
    const color = this.colorService.getColorScheme();

    this.graphCommunicationService.nodeTypes = {};

    let newGraph = new GraphData(graph);

    newGraph = GraphHelperService.preProcessGraph(newGraph);
    newGraph.elements.forEach((e, i) => {
      e.padding = 32;
      e.fixed = false;
      if (!this.graphCommunicationService.nodeTypes[e.type]) {
        this.graphCommunicationService.nodeTypes[e.type] = color[colorsUsed];
        colorsUsed = (colorsUsed + 1) % color.length;
      }
    });

    return newGraph;
  }

  private updateColors() {
    let colorsUsed = 0;
    const color = this.colorService.getColorScheme();
    const graph = this.graph;
    this.graph = undefined;
    if (graph) {
      this.graphCommunicationService.nodeTypes = [];
      this.graph.elements.forEach((e, i) => {
        e.padding = 32;
        e.fixed = false;
        if (!this.graphCommunicationService.nodeTypes[e.type]) {
          this.graphCommunicationService.nodeTypes[e.type] = color[colorsUsed];
          colorsUsed = (colorsUsed + 1) % color.length;
        }
      });
    }
    setTimeout(() =>
        this.graph = graph
      , 0);

  }

}



